import { post, Delete} from '../axios/ajax'

export const urls = {
  loginToken: '/tokens',    //登录后获取token
  loginOutToken: '/tokens/',   //注销token时需要传递用户名
  loginUserInfo: '/sysuser/chakan',   //查看当前登录用户详情
  getFunctionByUserId: '/role/getFunctionByUserId',  //根据token获取后台中登录用户的权限
  getButtonShiroByUserId: '/role/getButtonShiroByUserId',   //根据token获取后台中登录用户可以操作的按钮
  oneUserOnePhone: '/mobilebind/userQuery'   //根据当前登录用户id判断其imei
}

/**
 * 用户登录后获取token
 * @param params
 * @param callback
 */
export const login = (params, callback) => { post(urls.loginToken, params, {'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'}, callback) }

/**
 * 用户注销时，删除token
 * @param params
 * @param callback
 */
export const logoutCurrentToken = (params, callback) => { Delete(urls.loginOutToken + params, {}, {'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'}, callback) }

/**
 * 查看当前登录用户详细情况
 * @param params
 * @param callback
 */
export const getLoginUserInfo = (callback) => { post(urls.loginUserInfo, {}, {'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'}, callback) }

/**
 * 根据token获取后台中登录用户的菜单
 * @param callback
 */
export const getMenuPermissionByToken = (callback) => { post(urls.getFunctionByUserId, {Mark: '2'}, {'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'}, callback) }

/**
 * 根据token获取后台中登录用户可以操作的按钮
 * @param callback
 */
export const getButtonPermissionByToken = (callback) => { post(urls.getButtonShiroByUserId, {Mark: '2'}, {'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'}, callback) }

/**
 * 根据当前用户id判断用户是否注册
 * @param params
 * @param callback
 */
export const isSupportOnePhone = (params, callback) => { post(urls.oneUserOnePhone, params, {'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'}, callback)}



