<template>
  <div class="has_nav_container">
    <PubNavBar title="创建巡视"></PubNavBar>
    <div class="syjc_main">
      <div class="syjc_form">
        <div class="syjc_form_item">
          <yd-cell-group>
            <yd-cell-item>
              <span slot="left">组织机构</span>
              <span slot="right">
                <yd-input v-model="processShowValue.orgName" placeholder="请选择组织机构" :readonly="true" @click.native="selectUserGroup"></yd-input>
              </span>
            </yd-cell-item>
            <yd-cell-item v-show="showModel.projectItemSelectTree">
              <span slot="left">分部分项</span>
              <span slot="right">
                <yd-input v-model="processShowValue.projectItemName" placeholder="请选择分部分项" :readonly="true" @click.native="selectProjectItem"></yd-input>
              </span>
            </yd-cell-item>
            <yd-cell-item v-show="showModel.pollingDetail">
              <span slot="left">所在里程:</span>
              <span slot="right">{{processShowValue.station}}</span>
            </yd-cell-item>
            <yd-cell-item v-show="showModel.pollingDetail">
              <span slot="left">巡视范围:</span>
              <yd-textarea slot="right" v-model="polling.pollingRange" placeholder="请输入巡视范围" maxlength="100"></yd-textarea>
            </yd-cell-item>
            <yd-cell-item v-show="showModel.pollingDetail">
              <span slot="left">主要施工:</span>
              <yd-textarea slot="right" v-model="polling.constructionSituation" placeholder="请输入主要施工情况" maxlength="100"></yd-textarea>
            </yd-cell-item>
            <yd-cell-item v-show="showModel.pollingDetail">
              <span slot="left">质量/安全:</span>
              <yd-textarea slot="right" v-model="polling.qualitySafety" placeholder="请输入质量/安全环保等情况" maxlength="100"></yd-textarea>
            </yd-cell-item>
            <yd-cell-item v-show="showModel.pollingDetail">
              <span slot="left">处理意见:</span>
              <yd-textarea slot="right" v-model="polling.questionResultIdea" placeholder="请输入发现的问题及处理意见" maxlength="100"></yd-textarea>
            </yd-cell-item>
            <yd-cell-item v-show="showModel.pollingDetail">
              <span slot="left">巡视描述:</span>
              <yd-textarea slot="right" v-model="polling.remark" placeholder="请输入巡视描述" maxlength="100"></yd-textarea>
            </yd-cell-item>
          </yd-cell-group>
        </div>
        <div class="syjc_att">
          <yd-cell-group>
            <yd-cell-item v-show="showModel.pollingDetail">
              <span slot="left">照片描述:</span>
              <yd-textarea slot="right" v-model="polling.photoDescribe" placeholder="请输入照片描述" maxlength="100"></yd-textarea>
            </yd-cell-item>
          </yd-cell-group>
          <Attach :attachList="polling.pollingFileList" :delAttachList="polling.delPollingFileList" :readonly="showModel.attachReadonly" :sourceType="2"></Attach>
        </div>
        <div class="syjc_bot">
          <yd-button v-show="showModel.pollingDetail" style="float: left;" type="primary" shape="circle" @click.native="subimtPollingInfo('polling')">创建巡视</yd-button>
          <yd-button v-show="showModel.pollingDetail" style="float: right;" type="primary" shape="circle" @click.native="subimtPollingInfo('command')">发起指令</yd-button>
        </div>
      </div>
    </div>
    <!-- 弹框中的组织机构 -->
    <yd-popup v-model="isShowUserGroup" position="center" width="90%">
      <vue-ztree :list.sync='userGroup' :func="ztreeNodeClick" :is-open='true'></vue-ztree>
    </yd-popup>
    <!-- 弹框中的工程分部分项 -->
    <yd-popup v-model="isShowProjectItem" position="center" width="90%">
      <vue-ztree :list.sync='projectItem' :func="ztreeProjectItemNodeClick" :is-open='true'></vue-ztree>
    </yd-popup>
  </div>

</template>

<script>
import Attach from '../../../components/pub/Attach'
import {getUserDepartZtree, getProjectItemListByOrgId, createPolling} from '../../../api/polling'
import { getGeolocation } from '../../../assets/plus/geolocation'
export default {
  data () {
    return {
      isShowUserGroup: false,
      isShowProjectItem: false,
      showModel: {
        projectItemSelectTree: false,
        remark: false,
        pollingDetail: false,
        attachReadonly: true
      },
      processShowValue: {
        orgName: '',
        projectItemName: '',
        station: ''  //里程
      },
      userGroup: [],
      projectItem: [],
      polling: {
        orgId: '',
        projectItemId: '',
        remark: '',
        pollingRange: '',  //巡视范围
        constructionSituation: '',   //主要施工情况
        qualitySafety: '',  //质量/安全环保等情况
        questionResultIdea: '', // 发现的问题及处理意见
        pollingFileList: [],
        delPollingFileList: [],
        lng: '',
        lat: '',
        photoLocation: '',
        photoDescribe: '',  //照片描述
        type: 'polling'
      }
    }
  },
  components: {
    Attach: Attach
  },
  created(){
    this.loadLocation();
  },
  methods: {
    loadLocation(){
      getGeolocation(res => {
        this.polling.photoLocation = res.addresses
        this.polling.lng = res.coords.longitude
        this.polling.lat = res.coords.latitude
      })
    },
    selectUserGroup: function () {
      this.isShowUserGroup = true
      this.initUserGroupZtree()
    },
    initUserGroupZtree: function () {
      getUserDepartZtree(res => {
        this.userGroup = res.data
      })
    },
    ztreeNodeClick: function (m) {
      m.hover = false
      let node = JSON.parse(JSON.stringify(m))
      if (node.children.length===0) {
        this.processShowValue.orgName = m.name
        this.polling.orgId = m.id
        this.isShowUserGroup = false
        this.showModel.projectItemSelectTree = true
      } else {
        this.$dialog.toast({
          mes: '施工单位下才有分部分项呢',
          timeout: 1500
        })
      }
    },
    selectProjectItem: function () {
      this.isShowProjectItem = true
      this.initProjectItemZtree(this.polling.orgId)
    },
    ztreeProjectItemNodeClick: function (m) {
      m.hover = false
      let node = JSON.parse(JSON.stringify(m))
      if (node.children.length === 0) {
        let startStation, endStation;
        if((m.startStation!==null && m.startStation!==undefined && m.startStation!=='') && (m.endStation!==null && m.endStation!==undefined && m.endStation!=='')){
          this.processShowValue.station=m.startStation+'~'+m.endStation;
        }
        this.processShowValue.projectItemName = m.name
        this.polling.projectItemId = m.id
        this.isShowProjectItem = false
        this.showModel.pollingDetail = true
        this.showModel.attachReadonly = false
      } else {
        this.$dialog.toast({
          mes: '子项工程可巡检',
          timeout: 1500
        })
      }
    },
    initProjectItemZtree: function (orgId) {
      this.loadLocation();
      getProjectItemListByOrgId({orgId: orgId}, res=>{
        this.projectItem = res.data
      })
    },
    subimtPollingInfo: function (state) {
      if (this.polling.pollingFileList.length <= 0) {
        this.$dialog.toast({
          mes: '请选择留底图片',
          timeout: 1500
        })
        return false
      }
      let formData = new FormData()
      formData.append('projectItemId', this.polling.projectItemId)
      //巡视的范围
      formData.append('pollingRange', this.polling.pollingRange);
      formData.append('constructionSituation', this.polling.constructionSituation);
      formData.append('qualitySafety', this.polling.qualitySafety);
      formData.append('questionResultIdea', this.polling.questionResultIdea);
      formData.append('describe', this.polling.remark)
      formData.append('lgt', this.polling.lng)
      formData.append('lat', this.polling.lat)
      formData.append('photoLocation', this.polling.photoLocation)
      formData.append('photoDescribe', this.polling.photoDescribe)
      formData.append('type', this.polling.type)
      for (let resourece of this.polling.pollingFileList) {
        if (resourece.isNew) {
          formData.append('files', resourece.file)
        }
      }
      createPolling(formData, res=>{
        if(res.ok) {
          if(state==='polling'){
            this.$dialog.toast({
              mes: '巡视成功',
              timeout: 1500
            })
            this.$router.push('/pollingList')
          }
          if(state==='command'){
            this.$dialog.toast({
              mes: '开始创建指令',
              timeout: 1500
            });
            this.$router.push('/createCommand/' + res.data)
          }
        }else{
          this.$dialog.toast({
            mes: '巡视失败',
            timeout: 2000,
            icon: 'error'
          })
        }
      })
    }
  }
}
</script>

<style scoped>

</style>



