<template>
  <div class="cs_contain has_nav_container">
    <PubNavBar title="指令详情"></PubNavBar>
    <div class="syjc_main">
      <div class="syjc_form">
        <div class="syjc_form_item">
          <yd-cell-group>
            <yd-cell-item>
              <span slot="left">分部分项：</span>
              <span slot="right">{{commandInfoDetail.projectItem}}</span>
            </yd-cell-item>
            <yd-cell-item>
              <span slot="left">创建人：</span>
              <span slot="right">{{commandInfoDetail.realname}}</span>
            </yd-cell-item>
            <yd-cell-item>
              <span slot="left">创建时间：</span>
              <span slot="right">{{commandInfoDetail.createTime}}</span>
            </yd-cell-item>
            <yd-cell-item>
              <span slot="left">计划完成时间：</span>
              <span slot="right">{{commandInfoDetail.planTime}}</span>
            </yd-cell-item>
            <yd-cell-item>
              <span slot="left">创建描述：</span>
              <span slot="right">{{commandInfoDetail.remark}}</span>
            </yd-cell-item>
            <fieldset style="border: 1px dashed #E6E6E6; margin: 10px; border-radius: 10px;">
              <legend>指令时间轴[<img :src="this.imgList[0].src" slot="icon"/>指令发起;<img :src="this.imgList[1].src" slot="icon"/>指令转发;<img :src="this.imgList[2].src" slot="icon"/>指令接收;<img :src="this.imgList[3].src" slot="icon"/>指令完成]</legend>
              <yd-timeline v-for="(item, index) in commandInfoDetail.commandUser" :key="index">
                <yd-timeline-item >
                  <img :src="convertImage(item.commandStagePeople)" slot="icon"/>
                  <p>{{item.name}}   指令描述:{{item.remark}}</p>
                  <p style="margin-top: 10px;">{{item.createTime}}</p>
                </yd-timeline-item>
              </yd-timeline>
            </fieldset>
            <yd-cell-item>
              <span slot="left">发送指令影像资料</span>
              <span slot="right">
                <ul v-for="(item, key) in commandInfoDetail.pictureOfCommand" :key="key" class="imgULList">
                  <li>
                    <template v-if="item.fileType==='jpg'">
                      <yd-lightbox>
                        <yd-lightbox-img :src="item.picture" style="width: 60px; height: 60px;"></yd-lightbox-img>
                      </yd-lightbox>
                    </template>
                    <template v-else-if="item.fileType==='mp4'">
                      <video :src="item.picture" style="width: 60px; height: 60px;" @click="previewVideo(item)"></video>
                    </template>
                  </li>
                </ul>
              </span>
            </yd-cell-item>
            <yd-cell-item v-show="issolve==='0' && commandInfoDetail.commandState==='0'">
              <yd-button slot="left" type="danger" v-if="this.$ltx.permissionJudge('transpondCommand')" @click.native="transpondCommand">转发</yd-button>
              <yd-button slot="right" type="primary" v-if="this.$ltx.permissionJudge('finishCommand')" @click.native="finishCommand">完成</yd-button>
            </yd-cell-item>
            <yd-cell-item v-show="commandInfoDetail.commandState==='1'">
              <span slot="left">完成指令影像资料</span>
              <span slot="right">
                <ul v-for="(item, key) in commandInfoDetail.finishPictureOfCommand" :key="key" class="imgULList">
                  <li>
                    <template v-if="item.fileType==='jpg'">
                      <yd-lightbox>
                        <yd-lightbox-img :src="item.picture" style="width: 60px; height: 60px;"></yd-lightbox-img>
                      </yd-lightbox>
                    </template>
                    <template v-else-if="item.fileType==='mp4'">
                      <video :src="item.picture" style="width: 60px; height: 60px;" @click="previewVideo(item)"></video>
                    </template>
                  </li>
                </ul>
              </span>
            </yd-cell-item>
          </yd-cell-group>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {getCommandDetailById} from '../../api/command'
export default {
  name: "commandDetail",
  data(){
    return {
      commandId: '',
      tabState: '',
      issolve: '',
      commandInfoDetail:{},
      imgList:[{
        src: require('../../assets/img/command/start.png')
      },{
        src: require('../../assets/img/command/transpond.png')
      },{
        src: require('../../assets/img/command/receive.png')
      },{
        src: require('../../assets/img/command/finish.png')
      }]
    }
  },
  methods:{
    loadCommandDetailById(){
      getCommandDetailById({id:this.commandId}, res=>{
        if(res.ok){
          this.commandInfoDetail=res.data
        }
      })
    },
    convertImage(index){
      if(index==='1'){
        return this.imgList[0].src
      }else if(index==='2'){
        return this.imgList[1].src
      }else if(index==='3'){
        return this.imgList[2].src
      }else if(index==='4'){
        return this.imgList[3].src
      }
    },
    transpondCommand:function(){
      this.$router.push({
        path: '/transpondCommand',
        query: {
          commanduserId: this.commandInfoDetail.commanduserId
        }
      })
    },
    finishCommand:function(){
      console.log(this.commandInfoDetail)
      this.$router.push({
        path: '/finishCommand',
        query: {
          commandinfoid: this.commandInfoDetail.commandinfoid
        }
      })
    }
  },
  created(){
    this.commandId=this.$route.query.commandId
    this.tabState=this.$route.query.tabState
    this.issolve=this.$route.query.issolve
    this.loadCommandDetailById()
  }
}
</script>

<style scoped>
.imgULList{
  margin: 4px;
  padding: 2px;
  float:left;
  width: 30%;
  text-align: center;
}
</style>



