<template>
  <yd-layout>
    <div class="tabs_main">
      <router-view/>
    </div>
    <yd-tabbar slot="tabbar" :fixed="true" active-color="#275EF6" fontsize="14px">
      <yd-tabbar-item title="收到" :active="tab1.active" @click.native="activeTab(1)" >
        <img slot="icon" class="tabs_img" :src="tab1.src">
      </yd-tabbar-item>
      <yd-tabbar-item title="发出" :active="tab2.active" @click.native="activeTab(2)">
        <img slot="icon" class="tabs_img" :src="tab2.src">
      </yd-tabbar-item>
    </yd-tabbar>
  </yd-layout>
</template>

<script>
import PubNavBar from '../../../components/pub/NavBar'
export default {
  data() {
    return {
      active: 1,  //   当前第一个被选中
    }
  },
  comments: {
    PubNavBar: PubNavBar
  },
  computed: {
    tab1: function () {
      return {
        active: this.active == 1,
        src: this.active == 1 ? require('../../../assets/img/process/receive2.png') : require('../../../assets/img/process/receive.png'),
      }
    },
    tab2: function () {
      return {
        active: this.active == 2,
        src: this.active == 2 ? require('../../../assets/img/process/send2.png') : require('../../../assets/img/process/send.png'),
      }
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      if (to.path === '/receiveProcessTask') {
        vm.active = 1
      } else if (to.path === '/sendProcessTask') {
        vm.active = 2
      }
    })
  },
  methods: {
    activeTab(index) {
      let path = '/'
      switch (index) {
        case 1:
          this.active = 1
          path = '/receiveProcessTask'
          break;
        case 2:
          this.active = 2
          path = '/sendProcessTask'
          break;
        default:
          break
      }
      this.$router.replace(path)
    }
  }
}
</script>

<style scoped>
.tabs_main{
  margin-bottom: 50px;
}
.tabs_img{
  height: 26px;
}
</style>



