<template>
  <div class="has_nav_container">
    <PubNavBar title="巡检详情"></PubNavBar>
    <div class="syjc_main">
      <div class="syjc_map" id="attendMap">
        <baidu-map class="sm_map" :center="{lng: pollingDetail.pictureOfCommand[0].lgt, lat: pollingDetail.pictureOfCommand[0].lat}" :zoom="zoom">
          <bm-marker :position="{lng: pollingDetail.pictureOfCommand[0].lgt, lat: pollingDetail.pictureOfCommand[0].lat}" :dragging="false" animation="BMAP_ANIMATION_BOUNCE">
            <bm-label :content="pollingDetail.pictureOfCommand[0].photoLocation" :labelStyle="{color:'red', fontSize:'14px', textAlign: 'center'}" :offset="{width: -155, height: 30}"></bm-label>
          </bm-marker>
        </baidu-map>
      </div>
    </div>
    <div class="syjc_form">
      <div class="syjc_form_item">
        <yd-cell-group>
          <yd-cell-item>
            <span slot="left">
              <template v-if="pollingDetail.projectType==='1'">单位工程:</template>
              <template v-else-if="pollingDetail.projectType==='2'">分部工程:</template>
              <template v-else-if="pollingDetail.projectType==='3'">子分部工程:</template>
              <template v-else-if="pollingDetail.projectType==='4'">分项工程:</template>
              <template v-else-if="pollingDetail.projectType==='5'">子分项工程:</template>
            </span>
            <span slot="right">{{pollingDetail.projectItem}}</span>
          </yd-cell-item>
          <yd-cell-item>
            <span slot="left">桩号:</span>
            <span slot="right">{{pollingDetail.zhuanghao}}</span>
          </yd-cell-item>
          <yd-cell-item>
            <span slot="left">巡视范围:</span>
            <span slot="right">{{pollingDetail.pollingRange}}</span>
          </yd-cell-item>
          <yd-cell-item>
            <span slot="left">主要施工:</span>
            <span slot="right">{{pollingDetail.constructionSituation}}</span>
          </yd-cell-item>
          <yd-cell-item>
            <span slot="left">质量/安全:</span>
            <span slot="right">{{pollingDetail.qualitySafety}}</span>
          </yd-cell-item>
          <yd-cell-item>
            <span slot="left">处理意见:</span>
            <span slot="right">{{pollingDetail.questionResultIdea}}</span>
          </yd-cell-item>
          <yd-cell-item>
            <span slot="left">巡视描述:</span>
            <span slot="right">{{pollingDetail.describe}}</span>
          </yd-cell-item>
          <yd-cell-item>
            <span slot="left">巡视人:</span>
            <span slot="right">{{pollingDetail.createName}}</span>
          </yd-cell-item>
          <yd-cell-item>
            <span slot="left">巡视时间:</span>
            <span slot="right">{{pollingDetail.createTime}}</span>
          </yd-cell-item>
          <yd-cell-item>
            <span slot="left">巡视地理位置:</span>
            <span slot="right"><yd-icon name="location" size="18px" color="#777"></yd-icon>{{pollingDetail.pictureOfCommand[0].photoLocation}}</span>
          </yd-cell-item>
          <yd-cell-item>
            <span slot="left">影像资料</span>
            <span slot="right">
              <ul v-for="(item, key) in pollingDetail.pictureOfCommand" :key="key" class="imgULList">
                <li>
                  <template v-if="item.fileType==='jpg'">
                    <yd-lightbox>
                      <yd-lightbox-img :src="item.picture" style="width: 60px; height: 60px;"></yd-lightbox-img>
                    </yd-lightbox>
                  </template>
                  <template v-else-if="item.fileType==='mp4'">
                    <video :src="item.picture" style="width: 60px; height: 60px;" @click="previewVideo(item)"></video>
                  </template>
                </li>
              </ul>
            </span>
          </yd-cell-item>
        </yd-cell-group>
      </div>
    </div>
    <!--音频播放-->
    <yd-popup v-model="showVideo" position="center" width="90%">
      <div class="huixin_video">
        <div><video id="previewVideo" :src="videoSrc" autoplay controls></video></div>
      </div>
      <div class="huixin_close">
        <yd-button @click.native="showVideo = false">关闭</yd-button>
      </div>
    </yd-popup>
  </div>
</template>

<script>
import {getPollingDetail} from '../../../api/polling'
export default {
  data () {
    return {
      showVideo: false,
      videoSrc: '',
      pollingDetail: {
        lgt: '',
        lat: '',
        projectType: '',
        projectItem: '',
        zhuanghao: '',
        pollingRange: '',  //巡视范围
        constructionSituation: '',   //主要施工情况
        qualitySafety: '',  //质量/安全环保等情况
        questionResultIdea: '', // 发现的问题及处理意见
        describe: '',
        createName: '',
        createTime: '',
        photoLocation: '',
        pictureOfCommand: [{
          photoLocation: '',
          lgt: 0,
          lat: 0
        }]
      },
      zoom: 15
    }
  },
  props: {
    pollingId: {
      type: String
    }
  },
  methods: {
    loadPollingDetail: function () {
      getPollingDetail(this.pollingId, res=>{
        this.pollingDetail = res.data
        this.lng=res.data.pictureOfCommand[0].lgt
        this.lat=res.data.pictureOfCommand[0].lat
      })
    },
    previewVideo: function (item) {
      this.videoSrc = item.filePath
      this.showVideo = true
    }
  },
  created: function () {
    this.loadPollingDetail()
  }
}
</script>

<style scoped>
  .syjc_map{
    width: 100%;
    height: 200px;
    text-align: center;
    background-color: #fff;
    border-bottom: 1px solid #eee;
  }
  .sm_map{
    width: 100%;
    height: 100%;
  }
  .imgULList{
    margin: 10px;
    padding: 2px;
    float:left;
    width: 30%;
    text-align: center;
  }
</style>



