<template>
  <div class="cs_contain has_nav_container">
    <PubNavBar title="完成指令" ></PubNavBar>
    <div class="syjc_main">
      <div class="syjc_form">
        <div class="syjc_form_item">
          <yd-cell-group>
            <yd-cell-item>
              <span slot="left">指令描述：</span>
              <yd-textarea slot="right" v-model="finishCommandObj.remark" placeholder="请输入转发描述" maxlength="100"></yd-textarea>
            </yd-cell-item>
            <Attach :attachList="finishCommandObj.finishCommandPhotoList" :delAttachList="finishCommandObj.delFinishCommandPhotoList" :readonly="false" :sourceType="2"></Attach>
            <yd-button size="large" type="primary" shape="circle" @click.native="finishCommandFun">提交</yd-button>
          </yd-cell-group>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getGeolocation } from '../../../../assets/plus/geolocation'
import Attach from '../../../../components/pub/Attach'
import {finishCommand} from '../../../../api/command'
export default {
  name: "finishCommand",
  data(){
    return{
      finishCommandObj:{
        remark: '',
        commandid: '',
        lgt: '',
        lat: '',
        photoLocation: '',
        finishCommandPhotoList:[],
        delFinishCommandPhotoList:[]
      }
    }
  },
  components:{
    Attach:Attach
  },
  methods:{
    loadLocation(){
      getGeolocation(res => {
        this.finishCommandObj.photoLocation = res.addresses
        this.finishCommandObj.lng = res.coords.longitude
        this.finishCommandObj.lat = res.coords.latitude
      })
    },
    finishCommandFun(){
      if (this.commonFun.isEmptyStr(this.finishCommandObj.remark)) {
        this.$dialog.toast({
          mes: '请填写指令描述',
          timeout: 1500
        })
        return false
      }
      if (this.finishCommandObj.finishCommandPhotoList.length <= 0) {
        this.$dialog.toast({
          mes: '请选择留底图片',
          timeout: 1500
        })
        return false
      }
      let formData = new FormData()
      formData.append('commandid', this.finishCommandObj.commandid)
      formData.append('remark', this.finishCommandObj.remark)
      formData.append('lgt', this.finishCommandObj.lng)
      formData.append('lat', this.finishCommandObj.lat)
      formData.append('photoLocation', this.finishCommandObj.photoLocation)
      for (let resourece of this.finishCommandObj.finishCommandPhotoList) {
        if (resourece.isNew) {
          formData.append('files', resourece.file)
        }
      }
      finishCommand(formData, res=>{
        if(res.ok){
          this.$dialog.toast({
            mes: '完成指令成功',
            timeout: 1500
          })
          this.$router.push('/commandInfo')
        }else{
          this.$dialog.toast({
            mes: '完成指令失败',
            timeout: 2000,
            icon: 'error'
          })
        }
      })
    }
  },
  created(){
    this.finishCommandObj.commandid=this.$route.query.commandinfoid
    this.loadLocation()
  }
}
</script>

<style scoped>

</style>



