// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import axios from './axios'
import store from './store'
import './assets/plus/main'
import './assets/css/base.css'
import { getLoginUser } from './assets/js/assist'
import properties from './assets/js/properties'

import YDUI from 'vue-ydui'
import 'vue-ydui/dist/ydui.px.css'
/**   自定义图标  **/
import './assets/Iconfont/huixin-iconfont.css'
import commonFun from './assets/js/common'

import FastClick from 'fastclick'
import VueResource from 'vue-resource'
import VueHtml5Plus from 'vue-html5plus'
import cookies from 'js-cookie'
import vueztree from 'vue-ztree-2.0/dist/vue-ztree-2.0.umd.min.js'
import 'vue-ztree-2.0/dist/vue-ztree-2.0.css'
import VCharts from 'v-charts'
import AwesomePicker from 'vue-awesome-picker'
//处理app推送消息和跳转
import handlePush from './assets/plus/handlePush'
import permission from './assets/js/permission'
//自定义指令
import ltx from './util/permission'


import BaiduMap from 'vue-baidu-map'
Vue.use(BaiduMap, {
  ak: properties.baiduAk
})

Vue.prototype.$replace = function(path){ router.replace(path) }

//全局组件
import PubNavBar from "./components/pub/NavBar";

Vue.component('PubNavBar', PubNavBar)

Vue.prototype.cookies = cookies
Vue.prototype.$ltx=ltx
Vue.prototype.commonFun = commonFun
Vue.config.productionTip = false;
Vue.use(VueResource);
Vue.use(YDUI);
Vue.use(VueHtml5Plus);
Vue.use(vueztree);
Vue.use(VCharts);
Vue.use(AwesomePicker);

// 处理刷新的时候vuex被清空但是用户已经登录的情况
if (getLoginUser()) {
    store.dispatch('setUserInfo', getLoginUser());
}

FastClick.attach(document.body);

new Vue({
	el: '#app',
	router,
	store,
	components: { App },
	template: '<App/>'
})



