<template>
  <div class="has_nav_container">
    <PubNavBar title="自检"></PubNavBar>
    <div class="syjc_main">
      <div class="syjc_form">
        <div class="syjc_form_item">
          <yd-cell-group>
            <yd-cell-item>
              <span slot="left">分部分项：</span>
              <span slot="right">{{processInfoDetail.projectItem}}</span>
            </yd-cell-item>
            <yd-cell-item>
              <span slot="left">桩号：</span>
              <span slot="right">{{processInfoDetail.zhuanghao}}</span>
            </yd-cell-item>
            <yd-cell-item>
              <span slot="left">工序名称：</span>
              <span slot="right">{{processInfoDetail.processName}}</span>
            </yd-cell-item>
            <yd-cell-item>
              <span slot="left">状态：</span>
              <span slot="right">
                <template v-if="tabState==='task' ">
                  <template v-if="checkState===1">待自检</template>
                  <template v-else-if="checkState===2">待验收</template>
                  <template v-else-if="checkState===3">验收完成</template>
                </template>
              </span>
            </yd-cell-item>
            <fieldset style="border: 1px dashed #E6E6E6; margin: 10px; border-radius: 10px;">
              <legend>自检</legend>
              <yd-cell-item>
                <span slot="left">计划时间：</span>
                <span slot="right">{{processInfoDetail.planSelfCheckTime}}</span>
              </yd-cell-item>
              <yd-cell-item>
                <span slot="left">计划人：</span>
                <span slot="right">{{processInfoDetail.planSelfCheckPerson}}</span>
              </yd-cell-item>
              <yd-cell-item>
                <span slot="left">自检描述：</span>
                <yd-textarea slot="right" v-model="selfCheckProcess.remark" placeholder="请输入自检描述" maxlength="100"></yd-textarea>
              </yd-cell-item>
            </fieldset>
          </yd-cell-group>
        </div>
        <div class="syjc_att">
          <yd-cell-group>
            <yd-cell-item>
              <span slot="left">照片描述:</span>
              <yd-textarea slot="right" v-model="selfCheckProcess.photoDescribe" placeholder="请输入照片描述" maxlength="100"></yd-textarea>
            </yd-cell-item>
          </yd-cell-group>
          <Attach :attachList="selfCheckProcess.realProgressFile" :delAttachList="selfCheckProcess.delProgressList" :readonly="false" :sourceType="2"></Attach>
        </div>
        <div class="syjc_bot">
          <yd-button size="large" type="primary" shape="circle" @click.native="workerCheckFun">自检</yd-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Attach from '../../../../components/pub/Attach'
import PubNavBarTo from '../../../../components/pub/NavBarTo'
import {getProcessDetail, supervisionCheck} from '../../../../api/processCheck'
import { getGeolocation } from '../../../../assets/plus/geolocation'
export default {
  name: "selfCheckTask",
  data(){
    return {
      processId: '',
      checkState: '',
      showVideo: false,
      videoSrc: '',
      processInfoDetail: {},
      selfCheckProcess: {
        remark: '',
        photoDescribe: '',
        realProgressFile: [],
        delProgressList: [],
        lng: '',
        lat: '',
        photoLocation: '',
        adopt: ''
      }
    }
  },
  components:{
    Attach: Attach,
    PubNavBarTo: PubNavBarTo
  },
  methods:{
    loadLocation(){
      getGeolocation(res => {
        this.selfCheckProcess.photoLocation = res.addresses
        this.selfCheckProcess.lng = res.coords.longitude
        this.selfCheckProcess.lat = res.coords.latitude
      })
    },
    initProcessDetail: function () {
      getProcessDetail({id: this.processId}, res=>{
        this.processInfoDetail=res.data
      })
    },
    workerCheckFun: function () {
      if (this.commonFun.isEmptyStr(this.selfCheckProcess.remark)) {
        this.$dialog.toast({
          mes: '请填写自检描述',
          timeout: 1500
        })
        return false
      }
      if (this.selfCheckProcess.realProgressFile.length <= 0) {
        this.$dialog.toast({
          mes: '请选择留底图片',
          timeout: 1500
        })
        return false
      }
      let formData = new FormData()
      formData.append('processMainId', this.processId)
      formData.append('processId', this.processInfoDetail.processLogId)
      formData.append('remark', this.selfCheckProcess.remark)
      formData.append('lgt', this.selfCheckProcess.lng)
      formData.append('lat', this.selfCheckProcess.lat)
      formData.append('photoLocation', this.selfCheckProcess.photoLocation)
      formData.append('photoDescribe', this.selfCheckProcess.photoDescribe)
      formData.append('type', 'task')
      formData.append('mark', 2)
      formData.append('adopt', this.selfCheckProcess.adopt)  //此时没有通过不通过
      for (let resourece of this.selfCheckProcess.realProgressFile) {
        if (resourece.isNew) {
          formData.append('files', resourece.file)
        }
      }
      supervisionCheck(formData, res=>{
        if(res.ok){
          this.$dialog.toast({
            mes: '验收成功',
            timeout: 1500
          })
          this.$router.push('/receiveProcessTask')
        }else{
          this.$dialog.toast({
            mes: '验收失败',
            timeout: 2000,
            icon: 'error'
          })
        }
      })
    }
  },
  created(){
    this.processId=this.$route.query.processId
    this.checkState=this.$route.query.checkState
    this.initProcessDetail()
    this.loadLocation()
  }
}
</script>

<style scoped>

</style>



