import axios from 'axios'
import qs from 'qs'
import {getLoginUser} from '../assets/js/assist.js'


export const get = (url, params, headers, callback) => {
	axios.get(url, { params: params }, {headers: headers}).then((res) => {
		if(typeof callback == 'function' && res) {
      callback(res.data)
    }
	},(err) => {
		console.log(err);
	}).catch(this.handleError)
}
export const post = (url, params,headers, callback) => {
	axios.post(url, qs.stringify(params), headers||{}).then((res) => {
		if(typeof callback === 'function' && res) {
      callback(res.data)
    }
	},(err) => {
		console.log(err);
	}).catch(this.handleError)
}

export const Delete = (url, params,headers, callback) => {
  axios.delete(url, qs.stringify(params), headers||{}).then((res) => {
    if(typeof callback === 'function' && res) {
      callback(res.data)
    }
  },(err) => {
    console.log(err);
  }).catch(this.handleError)
}

export const json_post = (url, params, callback) => {
	axios.post(url, params, {headers: {"Content-Type": "application/json"}}).then((res) => {
		if(typeof callback == 'function' && res)
			callback(res.data)
	},(err) => {
		console.log(err);
	}).catch(this.handleError)

}

export const upload = (url, params, callback) => {
	axios.post(url, params, {headers: {'Content-Type': 'multipart/form-data'}}).then((res) => {
		if(typeof callback === 'function' && res) {
      callback(res.data)
    }
	},(err) => {
		console.log(err);
	}).catch(this.handleError)

}

export const download = (url, params) => { window.location.href = axios.defaults.baseURL + url + '?token=' + getLoginUser().token + '&' + qs.stringify(params) }

//可用于跨域请求
export const xhrrequest = (method, url, params, callback) => {
	var xhr = null;
	xhr = new plus.net.XMLHttpRequest();
	xhr.responseType = 'json';
	xhr.setRequestHeader('Content-Type', 'application/x-www-form-urlencoded');
	xhr.onreadystatechange = function () {
		switch ( xhr.readyState ) {
			case 0:
				break;
			case 1:
				break;
			case 2:
				break;
			case 3:
				break;
			case 4:
				//xhr请求已完成
				if ( xhr.status == 200 ) {
					callback(xhr.response);
				} else {
					console.log( "xhr请求失败："+xhr.status );
				}
				break;
			default :
				break;
		}
	}
	xhr.open( method?method:"GET", url );
	xhr.send(JSON.stringify(params));
}



