let commonFun = {}

/**
 * 判断字符串为空
 * @param str
 * @returns {boolean}
 */
commonFun.isEmptyStr = function (str) {
  if ((undefined === str) || (str === null) || (str === '') || (str.trim().length === 0)) {
    return true
  } else {
    return false
  }
}

/**
 * 判断字符串不能为空
 * @param str
 * @returns {boolean}
 */
commonFun.isNotEmptyStr = function (str) {
  if ((str !== '') && (str !== null) && (str !== undefined) && str.trim().length > 0) {
    return true
  } else {
    return false
  }
}

/**
 * uuid
 * @returns {string}
 * @constructor
 */
commonFun.UUID=function () {
  function S4() {
    return (((1+Math.random())*0x10000)|0).toString(16).substring(1)
  }
  return (S4()+S4()+"-"+S4()+"-"+S4()+"-"+S4()+"-"+S4()+S4()+S4())
}
export default commonFun



