<template>
  <div class="has_nav_container">
    <PubNavBar title="创建旁站巡视"></PubNavBar>
    <div class="syjc_main">
      <div class="syjc_form">
        <div class="syjc_form_item">
          <yd-cell-group>
            <yd-cell-item>
              <span slot="left">组织机构</span>
              <span slot="right">
                <yd-input v-model="processShowValue.orgName" placeholder="请选择组织机构" :readonly="true" @click.native="selectUserGroup"></yd-input>
              </span>
            </yd-cell-item>
            <yd-cell-item v-show="showModel.projectItemSelectTree">
              <span slot="left">分部分项</span>
              <span slot="right">
                <yd-input v-model="processShowValue.projectItemName" placeholder="请选择分部分项" :readonly="true" @click.native="selectProjectItem"></yd-input>
              </span>
            </yd-cell-item>
            <yd-cell-item v-show="showModel.sideStationPollingDetail">
              <span slot="left">所在里程:</span>
              <span slot="right">{{processShowValue.station}}</span>
            </yd-cell-item>
            <yd-cell-item v-show="showModel.sideStationPollingDetail">
              <span slot="left">旁站项目:</span>
              <yd-textarea slot="right" v-model="sideStationPolling.sideStationProject" placeholder="请输入旁站项目" maxlength="100"></yd-textarea>
            </yd-cell-item>
            <yd-cell-item v-show="showModel.sideStationPollingDetail">
              <span slot="left">施工过程:</span>
              <yd-textarea slot="right" v-model="sideStationPolling.sideStationSketch" placeholder="请输入施工过程简述" maxlength="100"></yd-textarea>
            </yd-cell-item>
            <yd-cell-item v-show="showModel.sideStationPollingDetail">
              <span slot="left">旁站工作:</span>
              <yd-textarea slot="right" v-model="sideStationPolling.constructionSituation" placeholder="请输入旁站工作情况" maxlength="100"></yd-textarea>
            </yd-cell-item>
            <yd-cell-item v-show="showModel.sideStationPollingDetail">
              <span slot="left">主要数据:</span>
              <yd-textarea slot="right" v-model="sideStationPolling.sideStationDataRecord" placeholder="请输入主要数据记录" maxlength="100"></yd-textarea>
            </yd-cell-item>
            <yd-cell-item v-show="showModel.sideStationPollingDetail">
              <span slot="left">处理结果:</span>
              <yd-textarea slot="right" v-model="sideStationPolling.questionResultIdea" placeholder="请输入发现的问题及处理结果" maxlength="100"></yd-textarea>
            </yd-cell-item>
            <yd-cell-item v-show="showModel.sideStationPollingDetail">
              <span slot="left">巡视描述:</span>
              <yd-textarea slot="right" v-model="sideStationPolling.remark" placeholder="请输入巡视描述" maxlength="100"></yd-textarea>
            </yd-cell-item>
          </yd-cell-group>
        </div>
        <div class="syjc_att">
          <yd-cell-group>
            <yd-cell-item v-show="showModel.sideStationPollingDetail">
              <span slot="left">照片描述:</span>
              <yd-textarea slot="right" v-model="sideStationPolling.photoDescribe" placeholder="请输入照片描述" maxlength="100"></yd-textarea>
            </yd-cell-item>
          </yd-cell-group>
          <Attach :attachList="sideStationPolling.pollingFileList" :delAttachList="sideStationPolling.delPollingFileList" :readonly="showModel.attachReadonly" :sourceType="2"></Attach>
        </div>
        <div class="syjc_bot">
          <yd-button v-show="showModel.pollingDetail" style="float: left;" type="primary" shape="circle" @click.native="subimtPollingInfo('sideStation')">创建巡视</yd-button>
          <yd-button v-show="showModel.pollingDetail" style="float: right;" type="primary" shape="circle" @click.native="subimtPollingInfo('command')">发起指令</yd-button>
        </div>
      </div>
    </div>
    <!-- 弹框中的组织机构 -->
    <yd-popup v-model="isShowUserGroup" position="center" width="90%">
      <vue-ztree :list.sync='userGroup' :func="ztreeNodeClick" :is-open='true'></vue-ztree>
    </yd-popup>
    <!-- 弹框中的工程分部分项 -->
    <yd-popup v-model="isShowProjectItem" position="center" width="90%">
      <vue-ztree :list.sync='projectItem' :func="ztreeProjectItemNodeClick" :is-open='true'></vue-ztree>
    </yd-popup>
  </div>

</template>

<script>
  import Attach from '../../../components/pub/Attach'
  import {getUserDepartZtree, getProjectItemListByOrgId, createPolling} from '../../../api/polling'
  import { getGeolocation } from '../../../assets/plus/geolocation'
  export default {
    data () {
      return {
        isShowUserGroup: false,
        isShowProjectItem: false,
        showModel: {
          projectItemSelectTree: false,
          remark: false,
          sideStationPollingDetail: false,
          attachReadonly: true
        },
        processShowValue: {
          orgName: '',
          projectItemName: '',
          station: ''  //里程
        },
        userGroup: [],
        projectItem: [],
        sideStationPolling: {
          orgId: '',
          projectItemId: '',
          remark: '',
          sideStationProject: '',  //旁站项目
          sideStationSketch: '',   //施工过程简述
          constructionSituation: '',  //旁站工作情况
          sideStationDataRecord: '',  //主要数据记录
          questionResultIdea: '', // 发现的问题及处理意见
          pollingFileList: [],
          delPollingFileList: [],
          lng: '',
          lat: '',
          photoLocation: '',
          photoDescribe: '',  //照片描述
          type: 'sideStation'
        }
      }
    },
    components: {
      Attach: Attach
    },
    created(){
      this.loadLocation();
    },
    methods: {
      loadLocation(){
        getGeolocation(res => {
          this.sideStationPolling.photoLocation = res.addresses
          this.sideStationPolling.lng = res.coords.longitude
          this.sideStationPolling.lat = res.coords.latitude
        })
      },
      selectUserGroup: function () {
        this.isShowUserGroup = true
        this.initUserGroupZtree()
      },
      initUserGroupZtree: function () {
        getUserDepartZtree(res => {
          this.userGroup = res.data
        })
      },
      ztreeNodeClick: function (m) {
        m.hover = false
        let node = JSON.parse(JSON.stringify(m))
        if (node.orgTpye === '4') {
          this.processShowValue.orgName = m.name
          this.sideStationPolling.orgId = m.id
          this.isShowUserGroup = false
          this.showModel.projectItemSelectTree = true
        } else {
          this.$dialog.toast({
            mes: '施工单位下才有分部分项呢',
            timeout: 1500
          })
        }
      },
      selectProjectItem: function () {
        this.isShowProjectItem = true
        this.initProjectItemZtree(this.sideStationPolling.orgId)
      },
      ztreeProjectItemNodeClick: function (m) {
        m.hover = false
        let node = JSON.parse(JSON.stringify(m))
        if (node.children.length === 0) {
          let startStation, endStation;
          if((m.startStation!==null && m.startStation!==undefined && m.startStation!=='') && (m.endStation!==null && m.endStation!==undefined && m.endStation!=='')){
            this.processShowValue.station=m.startStation+'~'+m.endStation;
          }
          this.processShowValue.projectItemName = m.name
          this.sideStationPolling.projectItemId = m.id
          this.isShowProjectItem = false
          this.showModel.sideStationPollingDetail = true
          this.showModel.attachReadonly = false
        } else {
          this.$dialog.toast({
            mes: '子项工程可旁站巡视',
            timeout: 1500
          })
        }
      },
      initProjectItemZtree: function (orgId) {
        this.loadLocation();
        getProjectItemListByOrgId({orgId: orgId}, res=>{
          this.projectItem = res.data
        })
      },
      subimtPollingInfo: function (state) {
        if (this.sideStationPolling.pollingFileList.length <= 0) {
          this.$dialog.toast({
            mes: '请选择留底图片',
            timeout: 1500
          })
          return false
        }
        let formData = new FormData()
        formData.append('projectItemId', this.sideStationPolling.projectItemId)
        //巡视的范围
        formData.append('sideStationProject', this.sideStationPolling.sideStationProject);
        formData.append('sideStationSketch', this.sideStationPolling.sideStationSketch);
        formData.append('constructionSituation', this.sideStationPolling.constructionSituation);
        formData.append('sideStationDataRecord', this.sideStationPolling.sideStationDataRecord);
        formData.append('questionResultIdea', this.sideStationPolling.questionResultIdea);
        formData.append('describe', this.sideStationPolling.remark)
        formData.append('lgt', this.sideStationPolling.lng)
        formData.append('lat', this.sideStationPolling.lat)
        formData.append('photoLocation', this.sideStationPolling.photoLocation)
        formData.append('photoDescribe', this.sideStationPolling.photoDescribe)
        formData.append('type', this.sideStationPolling.type)
        for (let resourece of this.sideStationPolling.pollingFileList) {
          if (resourece.isNew) {
            formData.append('files', resourece.file)
          }
        }
        createPolling(formData, res=>{
          if(res.ok) {
            if(state==='sideStation') {
              this.$dialog.toast({
                mes: '旁站巡视成功',
                timeout: 1500
              });
              this.$router.push('/sideStationList')
            }
            if(state==='command') {
              this.$dialog.toast({
                mes: '开始创建指令',
                timeout: 1500
              })
              this.$router.push('/createCommand/' + res.data)
            }
          }else{
            this.$dialog.toast({
              mes: '旁站巡视失败',
              timeout: 2000,
              icon: 'error'
            })
          }
        })
      }
    }
  }
</script>

<style scoped>

</style>



