<template>
  <div class="has_nav_container">
    <PubNavBar title="发出验收任务" gotoVueName="/processTab"></PubNavBar>
    <div class="cs_search">
      <yd-flexbox>
        <yd-flexbox-item>
          <yd-cell-group>
            <yd-cell-item>
              <span slot="left">组织机构：</span>
              <yd-input slot="right" v-model="showValue.orgName" placeholder="请选择组织机构" :readonly="true" @click.native="selectUserGroup"></yd-input>
            </yd-cell-item>
            <yd-cell-item>
              <span slot="left">分部分项：</span>
              <yd-input slot="right" v-model="showValue.projectItemName" placeholder="请选择分部分项" :readonly="true" @click.native="selectProjectItem"></yd-input>
            </yd-cell-item>
          </yd-cell-group>
        </yd-flexbox-item>
        <div style="width: 60px;text-align: center;">
          <div class="huixin_search_btn" @click="search()">搜索</div>
        </div>
      </yd-flexbox>
    </div>
    <div class="zlxj_main">
      <yd-pullrefresh :callback="loadSendCheckTaskPullRefresh" ref="pullRefreshSend">
        <yd-infinitescroll :callback="loadSendCheckTaskList" ref="infinitescrollSend">
          <yd-list theme="4" slot="list">
            <yd-list-item v-for="(item, key) in sendCheckTaskList" :key="key" @click.native="findCheckTaskDetail(item,'task')">
              <img slot="img" :src="item.filePath"/>
              <span slot="title">{{item.processName}}</span>
              <yd-list-other slot="other">
                <ul>
                  <li class="list-price"><b>计划自检人:</b>{{item.planSelfCheckPerson}}</li>
                  <li class="list-price"><b>计划验收人:</b>{{item.planCheckPerson}}</li>
                  <li class="list-del-price"><b>验收描述:</b>{{item.checkDescribe}}</li>
                  <li class="list-del-price"><b>状态:</b>
                    <template v-if="item.state===1">待自检</template>
                    <template v-else-if="item.state===2">待验收</template>
                    <template v-else-if="item.state===3">完成</template>
                  </li>
                  <li class="list-del-price"><b>位置:</b><yd-icon name="location" size="18px" color="#777"></yd-icon>{{item.photoLocation}}</li>
                </ul>
              </yd-list-other>
            </yd-list-item>
          </yd-list>
          <!-- 数据全部加载完毕显示 -->
          <span slot="doneTip">全部加载完成</span>
        </yd-infinitescroll>
      </yd-pullrefresh>
    </div>
    <!-- 弹框中的组织机构 -->
    <yd-popup v-model="isShowUserGroup" position="center" width="90%">
      <vue-ztree :list.sync='userGroup' :func="ztreeNodeClick" :is-open='true'></vue-ztree>
    </yd-popup>
    <!-- 弹框中的工程分部分项 -->
    <yd-popup v-model="isShowProjectItem" position="center" width="90%">
      <vue-ztree :list.sync='projectItem' :func="ztreeProjectItemNodeClick" :is-open='true'></vue-ztree>
    </yd-popup>
  </div>
</template>

<script>
import PubNavBarTo from '../../../components/pub/NavBarTo'
import {getUserDepartZtree, getProjectItemListByOrgId} from '../../../api/polling'
import {getSendCheckTaskSearch} from '../../../api/processCheck'
export default {
  name: 'processTask',
  data () {
    return {
      showValue: {
        projectItemName: '',
        orgName: '',
      },
      isShowProjectItem: false,
      isShowUserGroup: false,
      orgId: '',   //选中的组织机构id
      projectItem: [],
      userGroup: [],
      sendCheckTaskList: [],
      searchInfo: {
        projectItemCode: '',
        pageNo: 1,
        pageSize: 15
      }
    }
  },
  components: {
    PubNavBarTo: PubNavBarTo
  },
  methods: {
    selectUserGroup: function () {
      this.isShowUserGroup = true
      this.initUserGroupZtree()
    },
    initUserGroupZtree: function () {
      getUserDepartZtree(res => {
        this.userGroup = res.data
      })
    },
    ztreeNodeClick: function (m) {
      m.hover = false
      let node = JSON.parse(JSON.stringify(m))
      if (node.children.length===0) {
        this.showValue.orgName = m.name
        this.orgId = m.id
        this.isShowUserGroup = false
      } else {
        this.$dialog.toast({
          mes: '施工单位下才有分部分项呢',
          timeout: 1500
        })
      }
    },
    startSelfCheck: function () {
      this.$router.push('/startSelfCheck')
    },
    selectProjectItem: function () {
      this.isShowProjectItem = true
      this.initProjectItemZtree(this.orgId)
    },
    ztreeProjectItemNodeClick: function (m) {
      m.hover = false
      this.showValue.projectItemName = m.name
      this.searchInfo.projectItemCode = m.projectCode
      this.isShowProjectItem = false
    },
    initProjectItemZtree: function (orgId) {
      getProjectItemListByOrgId({orgId: orgId}, res=>{
        this.projectItem = res.data
      })
    },
    search:function () {
      this.sendCheckTaskList = []
      this.searchInfo.pageNo = 1
      this.$refs.infinitescrollSend.$emit('ydui.infinitescroll.reInit')
      this.loadSendCheckTaskList()
    },
    loadSendCheckTaskList:function () {   //滚动刷新
      getSendCheckTaskSearch(this.searchInfo, res=>{
        if(res.ok) {
          if(res.data && res.data.data.length > 0){
            const _list = res.data.data;
            this.sendCheckTaskList = [..._list, ...this.sendCheckTaskList];
            /* 单次请求数据完毕 */
            this.$refs.infinitescrollSend.$emit('ydui.infinitescroll.finishLoad');
            this.searchInfo.pageNo++
          }else{
            /* 所有数据加载完毕 */
            this.$refs.infinitescrollSend.$emit('ydui.infinitescroll.loadedDone');
          }
        }
      })
    },
    loadSendCheckTaskPullRefresh: function(){
      getSendCheckTaskSearch(this.searchInfo, res=>{
        if(res.ok) {
          if(res.data && res.data.data.length > 0){
            const _list = res.data.data;
            this.sendCheckTaskList = [..._list, ...this.sendCheckTaskList];
            /* 单次请求数据完毕 */
            this.$refs.pullRefreshSend.$emit('ydui.pullrefresh.finishLoad');
            this.searchInfo.pageNo++
          }else{
            /* 所有数据加载完毕 */
            this.$dialog.toast({
              mes:  '已是最新内容'
            });
          }
        }
      })
    },
    findCheckTaskDetail: function (item, state) {
      this.$router.push({
        path: '/processDetail',
        query: {
          processId: item.id,
          checkState: item.state
        }
      })
    }
  },
  created () {
    this.loadSendCheckTaskList()
  }
}
</script>

<style scoped>

</style>



