import {post} from '../axios/ajax'

export const urls = {
  initClientId: '/igexinPush/initClientId'  //初始化clientId
}

/**
 * 服务器注册个推信息
 * @param params
 * @param callback
 */
export const initGetui = (params, callback) => { post(urls.initClientId, params, {'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'}, callback) }



