<template>
  <div class="cs_contain has_nav_container">
    <PubNavBar title="巡检" :rightFun="this.$ltx.permissionJudge('createPolling')" customeIcon="footprint" @customeIconFun="createPolling()"></PubNavBar>
    <div class="cs_main">
      <yd-pullrefresh :callback="loadPollingListPullRefresh" ref="pullrefreshDemo">
        <yd-infinitescroll :callback="loadPollingList" ref="infinitescrollDemo">
          <yd-list theme="4" slot="list">
            <yd-list-item v-for="(item, key) in pollingDataList" :key="key" @click.native="showDetail(item.id)">
              <img slot="img" :src="item.filePath"/>
              <span slot="title">{{item.describe}}</span>
              <yd-list-other slot="other">
                <div>
                  <span class="list-price"><b>桩号:</b>{{item.startStation}}~{{item.endStation}}     <em>分项工程:</em>{{item.projectItem}}</span>
                  <br/>
                  <span class="list-del-price">{{item.createTime}}</span>
                  <br/>
                  <span class="list-del-price"><yd-icon name="location" size="18px" color="#777"></yd-icon>{{item.photoLocation}}</span>
                </div>
              </yd-list-other>
            </yd-list-item>
          </yd-list>
          <!-- 数据全部加载完毕显示 -->
          <span slot="doneTip">全部加载完成</span>
        </yd-infinitescroll>
      </yd-pullrefresh>
    </div>
  </div>
</template>

<script>
import {getPollingList} from '../../../api/polling'
export default {
  data () {
    return {
      pollingDataList: [],
      searchData: {
        pageNo: 1,
        pageSize: 15,
        startTime: '',
        endTime: '',
        userId: '',
        type: 'polling'
      }
    }
  },
  methods: {
    loadPollingList: function () {
      getPollingList(this.searchData, res => {
        if(res.ok) {
          if(res.data && res.data.data.length > 0){
            this.pollingDataList = this.pollingDataList.concat(res.data.data)
            /* 单次请求数据完毕 */
            this.$refs.infinitescrollDemo.$emit('ydui.infinitescroll.finishLoad');
            this.searchData.pageNo++
          }else{
            /* 所有数据加载完毕 */
            this.$refs.infinitescrollDemo.$emit('ydui.infinitescroll.loadedDone');
          }
        }
      })
    },
    loadPollingListPullRefresh: function(){
      getPollingList(this.searchData, res => {
        if(res.ok) {
          if(res.data && res.data.data.length > 0){
            this.pollingDataList = this.pollingDataList.concat(res.data.data)
            /* 单次请求数据完毕 */
            this.$refs.pullrefreshDemo.$emit('ydui.pullrefresh.finishLoad');
            this.searchData.pageNo++
          }else{
            /* 所有数据加载完毕 */
            this.$dialog.toast({
              mes:  '已是最新内容'
            });
          }
        }
      })
    },
    showDetail: function (id) {
      this.$router.push('/pollingDetail/' + id)
    },
    createPolling:function () {
      this.$router.push('/createPolling')
    }
  },
  created: function () {
    this.loadPollingList()
  }
}
</script>

<style scoped>

</style>



