const cookieTokenKey = 'TOKEN'

const sessionUserKey = 'userInfo'

/**
 * 将登录用户放到sessionStorage中
 * @param user
 */
function setLoginUser(user) {
	localStorage.setItem(cookieTokenKey, user)
	addCookie(cookieTokenKey, user, 24)
}
export {
	setLoginUser
}

/**
 * 保存登录账号密码
 *
 */
function setLoginInfo(account, password){
	localStorage.setItem("loginInfo", JSON.stringify({account: account, password: password}))
}
export {
	setLoginInfo
}

/**
 * 保存token
 *
 */
function setToken(token){
  localStorage.setItem("TOKEN", token)
}
export {
  setToken
}

/**
 * 获取登录账号密码
 *
 */
function getLoginInfo(){
	let loginInfo = localStorage.getItem("loginInfo")
	if(loginInfo) {
    return JSON.parse(loginInfo)
  }else {
    return null
  }
}
export {
	getLoginInfo
}

/**
 * 移除sessionStorage中的登录用户
 */
function removeLoginUser() {
	localStorage.removeItem(sessionUserKey)
	deleteCookie(cookieTokenKey)
}
export {
	removeLoginUser
}

/**
 * 移除sessionStorage中的登录用户
 */
function removeToken() {
  localStorage.removeItem(cookieTokenKey)
  deleteCookie(cookieTokenKey)
}
export {
  removeToken
}

/**
 * 获取登录用户信息
 */
function getLoginUser() {
	let userInfo = localStorage.getItem(sessionUserKey);
	if(userInfo) {
    return JSON.parse(userInfo)
  }else {
    return null
  }
}
export {
	getLoginUser
}

/**
 * 得到token
 * @returns {string}
 */
function getLoginToken() {
  let token= localStorage.getItem(cookieTokenKey)
  if(!token){
    return ''
  }
  return token
}
export {
  getLoginToken
}


/**
 * 添加cookie
 * @param name
 * @param value
 * @param expireHours 有效时间,单位小时,为0
 */
function addCookie(name, value, expireHours) {
	let cookieString = name + '=' + value
	// 判断是否设置过期时间
	if(expireHours > 0) {
		let date = new Date()
		date.setTime(date.getTime + expireHours * 3600 * 1000)
		cookieString = cookieString + '; expires=' + date.toGMTString()
	}
	document.cookie = cookieString
}

export {
	addCookie
}

/**
 * 获取cookie
 * @param name
 * @returns {*}
 */
function getCookie(name) {
	let strCookie = document.cookie
	let arrCookie = strCookie.split('; ')
	for(let i = 0; i < arrCookie.length; i++) {
		let arr = arrCookie[i].split('=')
		if(arr[0] === name) return arr[1]
	}
	return ''
}

export {
	getCookie
}

/**
 * 删除cookie, 即把value设置为空
 * @param name
 */
function deleteCookie(name) {
	let date = new Date()
	date.setTime(date.getTime() - 10000)
	document.cookie = name + '=v; expires=' + date.toGMTString()
}

export {
	deleteCookie
}

/**
 * 获取cookie中的token
 * @returns {*}
 */
function getTokenCookie() {
	return getCookie(cookieTokenKey)
}

export {
	getTokenCookie
}

/**
 * 删除cookie中的token
 */
function removeTokenCookie() {
	deleteCookie(cookieTokenKey)
}

export {
	removeTokenCookie
}

const defaultDatePattern = 'yyyy-MM-dd'

/**
 * 解析日期字符串为日期Date对象
 * @param dateStr 字符串日期
 * @param pattern 日期格式
 * @returns {Date}
 */
function parseStrToDate(dateStr, pattern) {
	if(!pattern || pattern === '') pattern = defaultDatePattern
	let date = new Date()
	if(pattern.includes('yyyy')) { // 年份
		date.setFullYear(Number(dateStr.substr(pattern.indexOf('yyyy'), 4)))
	}
	if(pattern.includes('MM')) { // 月份
		date.setMonth(Number(dateStr.substr(pattern.indexOf('MM'), 2)) - 1)
	}
	if(pattern.includes('dd')) { // 天
		date.setDate(Number(dateStr.substr(pattern.indexOf('dd'), 2)))
	}
	if(pattern.includes('HH')) { // 小时
		date.setHours(Number(dateStr.substr(pattern.indexOf('HH'), 2)))
	}
	if(pattern.includes('mm')) { // 分钟
		date.setMinutes(Number(dateStr.substr(pattern.indexOf('mm'), 2)))
	}
	if(pattern.includes('ss')) { // 秒
		date.setSeconds(Number(dateStr.substr(pattern.indexOf('ss'), 2)))
	}
	if(pattern.includes('SSS')) { // 毫秒
		date.setMilliseconds(Number(dateStr.substr(pattern.indexOf('SSS'), 3)))
	}
	return date
}

export {
	parseStrToDate
}

/**
 * 格式化日期
 * @param dat 日期对象
 * @param parttern 格式
 * @returns {*}
 */
function formatDate(dat, parttern) {
	if(!parttern || parttern === '') parttern = defaultDatePattern
	let o = {
		'M+': dat.getMonth() + 1, // 月份
		'd+': dat.getDate(), // 日
		'h+': dat.getHours() % 12 === 0 ? 12 : dat.getHours() % 12, // 小时
		'H+': dat.getHours(), // 小时
		'm+': dat.getMinutes(), // 分
		's+': dat.getSeconds(), // 秒
		'q+': Math.floor((dat.getMonth() + 3) / 3), // 季度
		'S': dat.getMilliseconds() // 毫秒
	};
	let week = {
		'0': '/u65e5',
		'1': '/u4e00',
		'2': '/u4e8c',
		'3': '/u4e09',
		'4': '/u56db',
		'5': '/u4e94',
		'6': '/u516d'
	};
	if(/(y+)/.test(parttern)) {
		parttern = parttern.replace(RegExp.$1, (dat.getFullYear() + '').substr(4 - RegExp.$1.length));
	}
	if(/(E+)/.test(parttern)) {
		parttern = parttern.replace(RegExp.$1, ((RegExp.$1.length > 1) ? (RegExp.$1.length > 2 ? '/u661f/u671f' : '/u5468') : '') + week[dat.getDay() + '']);
	}
	for(let k in o) {
		if(new RegExp('(' + k + ')').test(parttern)) {
			parttern = parttern.replace(RegExp.$1, (RegExp.$1.length === 1) ? (o[k]) : (('00' + o[k]).substr(('' + o[k]).length)));
		}
	}
	return parttern;
}

export {
	formatDate
}

/**
 * 获取某个日期范围内的所有日期
 * @param startDate
 * @param stopDate
 * @returns {Array}
 */
function getDates(startDate, stopDate, parttern) {
	if(!parttern || parttern === '') parttern = defaultDatePattern
	let dateArray = []
	let currentDate = startDate
	while(currentDate <= stopDate) {
		dateArray.push(formatDate(currentDate, parttern))
		currentDate = new Date(currentDate.setDate(currentDate.getDate() + 1))
	}
	return dateArray;
}

export {
	getDates
}

/**
 * 获取两个时间区间的天数
 * @param startDate
 * @param stopDate
 * @param parttern
 * @returns {Number}
 */
function getDays(startDate, stopDate, parttern) {
	if(!parttern || parttern === '') parttern = defaultDatePattern
	return getDates(parseStrToDate(startDate, parttern), parseStrToDate(stopDate, parttern)).length
}

export {
	getDays
}

/**
 * 日期转换为星期
 * 0和7代表星期天
 */
function timeObjToWeek(dateObj){
	let weeks = ['周日', '周一', '周二', '周三', '周四', '周五', '周六', '周日'];
	let week = dateObj.getDay();
	return weeks[week];
}

export{
	timeObjToWeek
}

/**
 * 天数加或减
 * @param date 日期对象
 * @param days 加减天数
 */
function addOrSubDate(date, days){
	date.setDate(date.getDate() + days);
	return date;
}

export {
	addOrSubDate
}

/**
 * 是否为空
 * */
function isEmptyStr(str) {
	if (undefined == str || null == str || "" == str || str.trim().length == 0) {
		return true;
	} else {
		return false;
	}
}
export {
	isEmptyStr
}

/**
 * 是否不为空
 * */
function isNotEmptyStr(str) {
	if (str && str.trim().length > 0) {
		return true;
	} else {
		return false;
	}
}
export {
	isNotEmptyStr
}

