/**
 * 日期转换为星期
 */
function timeToWeek(dateObj){
	let weeks = ['星期日', '星期一', '星期二', '星期三', '星期四', '星期五', '星期六'];
	let week = new Date(dateObj).getDay();
	return weeks[week];
}

export {
	timeToWeek
}

/**
 * 冒泡排序
 */
function bubbleSort(arr) {
  	let i = 0, len = arr.length, j, d;
  	for (; i < len; i++) {
    	for (j = 0; j < len; j++) {
      		if (arr[i] < arr[j]) {
        		d = arr[j]; arr[j] = arr[i]; arr[i] = d;
      		}
    	}
  	}
  	return arr;
}

export {
	bubbleSort
}



