<template>
  <div class="has_nav_container">
    <PubNavBar title="旁站巡视详情"></PubNavBar>
    <div class="syjc_main">
      <div class="syjc_map" id="attendMap">
        <baidu-map class="sm_map" :center="{lng: sideStationPollingDetail.pictureOfCommand[0].lgt, lat: sideStationPollingDetail.pictureOfCommand[0].lat}" :zoom="zoom">
          <bm-marker :position="{lng: sideStationPollingDetail.pictureOfCommand[0].lgt, lat: sideStationPollingDetail.pictureOfCommand[0].lat}" :dragging="false" animation="BMAP_ANIMATION_BOUNCE">
            <bm-label :content="sideStationPollingDetail.pictureOfCommand[0].photoLocation" :labelStyle="{color:'red', fontSize:'14px', textAlign: 'center'}" :offset="{width: -155, height: 30}"></bm-label>
          </bm-marker>
        </baidu-map>
      </div>
    </div>
    <div class="syjc_form">
      <div class="syjc_form_item">
        <yd-cell-group>
          <yd-cell-item>
            <span slot="left">
              <template v-if="sideStationPollingDetail.projectType==='1'">单位工程:</template>
              <template v-else-if="sideStationPollingDetail.projectType==='2'">分部工程:</template>
              <template v-else-if="sideStationPollingDetail.projectType==='3'">子分部工程:</template>
              <template v-else-if="sideStationPollingDetail.projectType==='4'">分项工程:</template>
              <template v-else-if="sideStationPollingDetail.projectType==='5'">子分项工程:</template>
            </span>
            <span slot="right">{{sideStationPollingDetail.projectItem}}</span>
          </yd-cell-item>
          <yd-cell-item>
            <span slot="left">桩号:</span>
            <span slot="right">{{sideStationPollingDetail.zhuanghao}}</span>
          </yd-cell-item>
          <yd-cell-item>
            <span slot="left">旁站项目:</span>
            <span slot="right">{{sideStationPollingDetail.sideStationProject}}</span>
          </yd-cell-item>
          <yd-cell-item>
            <span slot="left">施工过程:</span>
            <span slot="right">{{sideStationPollingDetail.sideStationSketch}}</span>
          </yd-cell-item>
          <yd-cell-item>
            <span slot="left">旁站工作:</span>
            <span slot="right">{{sideStationPollingDetail.constructionSituation}}</span>
          </yd-cell-item>
          <yd-cell-item>
            <span slot="left">主要数据:</span>
            <span slot="right">{{sideStationPollingDetail.sideStationDataRecord}}</span>
          </yd-cell-item>
          <yd-cell-item>
            <span slot="left">处理意见:</span>
            <span slot="right">{{sideStationPollingDetail.questionResultIdea}}</span>
          </yd-cell-item>
          <yd-cell-item>
            <span slot="left">巡视描述:</span>
            <span slot="right">{{sideStationPollingDetail.describe}}</span>
          </yd-cell-item>
          <yd-cell-item>
            <span slot="left">巡视人:</span>
            <span slot="right">{{sideStationPollingDetail.createName}}</span>
          </yd-cell-item>
          <yd-cell-item>
            <span slot="left">巡视时间:</span>
            <span slot="right">{{sideStationPollingDetail.createTime}}</span>
          </yd-cell-item>
          <yd-cell-item>
            <span slot="left">巡视地理位置:</span>
            <span slot="right"><yd-icon name="location" size="18px" color="#777"></yd-icon>{{sideStationPollingDetail.pictureOfCommand[0].photoLocation}}</span>
          </yd-cell-item>
          <yd-cell-item>
            <span slot="left">影像资料</span>
            <span slot="right">
              <ul v-for="(item, key) in sideStationPollingDetail.pictureOfCommand" :key="key" class="imgULList">
                <li>
                  <template v-if="item.fileType==='jpg'">
                    <yd-lightbox>
                      <yd-lightbox-img :src="item.picture" style="width: 60px; height: 60px;"></yd-lightbox-img>
                    </yd-lightbox>
                  </template>
                  <template v-else-if="item.fileType==='mp4'">
                    <video :src="item.picture" style="width: 60px; height: 60px;" @click="previewVideo(item)"></video>
                  </template>
                </li>
              </ul>
            </span>
          </yd-cell-item>
        </yd-cell-group>
      </div>
    </div>
    <!--音频播放-->
    <yd-popup v-model="showVideo" position="center" width="90%">
      <div class="huixin_video">
        <div><video id="previewVideo" :src="videoSrc" autoplay controls></video></div>
      </div>
      <div class="huixin_close">
        <yd-button @click.native="showVideo = false">关闭</yd-button>
      </div>
    </yd-popup>
  </div>
</template>

<script>
  import {getPollingDetail} from '../../../api/polling'
  export default {
    data () {
      return {
        showVideo: false,
        videoSrc: '',
        sideStationPollingDetail: {
          lgt: '',
          lat: '',
          projectType: '',
          projectItem: '',
          zhuanghao: '',
          pollingRange: '',  //巡视范围
          constructionSituation: '',   //主要施工情况
          qualitySafety: '',  //质量/安全环保等情况
          questionResultIdea: '', // 发现的问题及处理意见
          describe: '',
          createName: '',
          createTime: '',
          photoLocation: '',
          pictureOfCommand: [{
            photoLocation: '',
            lgt: 0,
            lat: 0
          }]
        },
        zoom: 15
      }
    },
    props: {
      pollingId: {
        type: String
      }
    },
    methods: {
      loadPollingDetail: function () {
        getPollingDetail(this.pollingId, res=>{
          this.sideStationPollingDetail = res.data
          this.lng=res.data.pictureOfCommand[0].lgt
          this.lat=res.data.pictureOfCommand[0].lat
        })
      },
      previewVideo: function (item) {
        this.videoSrc = item.filePath
        this.showVideo = true
      }
    },
    created: function () {
      this.loadPollingDetail()
    }
  }
</script>

<style scoped>
  .syjc_map{
    width: 100%;
    height: 200px;
    text-align: center;
    background-color: #fff;
    border-bottom: 1px solid #eee;
  }
  .sm_map{
    width: 100%;
    height: 100%;
  }
  .imgULList{
    margin: 10px;
    padding: 2px;
    float:left;
    width: 30%;
    text-align: center;
  }
</style>



