<template>
  <div class="has_nav_container">
    <PubNavBar title="验收详情"></PubNavBar>
    <div class="syjc_main">
      <div class="syjc_form">
        <div class="syjc_form_item">
          <yd-cell-group>
            <yd-cell-item>
              <span slot="left">分部分项：</span>
              <span slot="right">{{processInfoDetail.projectItem}}</span>
            </yd-cell-item>
            <yd-cell-item>
              <span slot="left">桩号：</span>
              <span slot="right">{{processInfoDetail.zhuanghao}}</span>
            </yd-cell-item>
            <yd-cell-item>
              <span slot="left">工序名称：</span>
              <span slot="right">{{processInfoDetail.processName}}</span>
            </yd-cell-item>
            <yd-cell-item>
              <span slot="left">状态：</span>
              <span slot="right">
                <template v-if="processInfoDetail.state1===0">已指定工序</template>
                <template v-else-if="processInfoDetail.state1===1">已指定验收计划</template>
                <template v-else-if="processInfoDetail.state1===2">已自检</template>
                <template v-else-if="processInfoDetail.state1===3">已验收</template>
              </span>
            </yd-cell-item>
            <fieldset style="border: 1px dashed #E6E6E6; margin: 10px; border-radius: 10px;">
              <legend style="font-weight: bold;">自检</legend>
              <yd-cell-item>
                <span slot="left">计划时间：</span>
                <span slot="right">{{processInfoDetail.planSelfCheckTime}}</span>
              </yd-cell-item>
              <yd-cell-item>
                <span slot="left">计划人：</span>
                <span slot="right">{{processInfoDetail.planSelfCheckPerson}}</span>
              </yd-cell-item>
              <yd-cell-item>
                <span slot="left">实际时间：</span>
                <span slot="right">{{processInfoDetail.realitySelfCheckTime}}</span>
              </yd-cell-item>
              <yd-cell-item>
                <span slot="left">实际人：</span>
                <span slot="right">{{processInfoDetail.realitySelfCheckPerson}}</span>
              </yd-cell-item>
              <yd-cell-item>
                <span slot="left">描述：</span>
                <span slot="right">{{processInfoDetail.selfCheckDescribe}}</span>
              </yd-cell-item>
              <yd-cell-item>
                <span slot="left">影像资料</span>
                <span slot="right">
                  <ul v-for="(item, key) in processInfoDetail.selfFilePath" :key="key" class="imgULList">
                    <li>
                      <template v-if="item.fileType==='jpg'">
                        <yd-lightbox>
                          <yd-lightbox-img :src="item.filePath" style="width: 60px; height: 60px;"></yd-lightbox-img>
                        </yd-lightbox>
                      </template>
                      <template v-else-if="item.fileType==='mp4'">
                        <video :src="item.filePath" style="width: 60px; height: 60px;" @click="previewVideo(item)"></video>
                      </template>
                    </li>
                  </ul>
                </span>
              </yd-cell-item>
            </fieldset>
            <fieldset style="border: 1px dashed #E6E6E6; margin: 10px; border-radius: 10px;">
              <legend style="font-weight: bold;">验收</legend>
              <yd-cell-item>
                <span slot="left">计划时间：</span>
                <span slot="right">{{processInfoDetail.planCheckTime}}</span>
              </yd-cell-item>
              <yd-cell-item>
                <span slot="left">计划人：</span>
                <span slot="right">{{processInfoDetail.planCheckPerson}}</span>
              </yd-cell-item>
              <yd-cell-item>
                <span slot="left">实际时间：</span>
                <span slot="right">{{processInfoDetail.realityCheckTime}}</span>
              </yd-cell-item>
              <yd-cell-item>
                <span slot="left">实际人：</span>
                <span slot="right">{{processInfoDetail.realityCheckPerson}}</span>
              </yd-cell-item>
              <yd-cell-item>
                <span slot="left">描述：</span>
                <span slot="right">{{processInfoDetail.checkDescribe}}</span>
              </yd-cell-item>
              <yd-cell-item>c
                <span slot="left">影像资料</span>
                <span slot="right">
                  <ul v-for="(item, key) in processInfoDetail.filePath" :key="key" class="imgULList">
                    <li>
                      <template v-if="item.fileType==='jpg'">
                        <yd-lightbox>
                          <yd-lightbox-img :src="item.filePath" style="width: 60px; height: 60px;"></yd-lightbox-img>
                        </yd-lightbox>
                      </template>
                      <template v-else-if="item.fileType==='mp4'">
                        <video :src="item.filePath" style="width: 60px; height: 60px;" @click="previewVideo(item)"></video>
                      </template>
                    </li>
                  </ul>
                </span>
              </yd-cell-item>
            </fieldset>
          </yd-cell-group>
        </div>
      </div>
    </div>
    <!--音频播放-->
    <yd-popup v-model="showVideo" position="center" width="90%">
      <div class="huixin_video">
        <div><video id="previewVideo" :src="videoSrc" autoplay controls></video></div>
      </div>
      <div class="huixin_close">
        <yd-button @click.native="showVideo = false">关闭</yd-button>
      </div>
    </yd-popup>
  </div>
</template>

<script>
import {getProcessDetail} from '../../api/processCheck'
export default {
  name: "processDetail",
  data(){
    return{
      processId: '',
      checkState: '',
      showVideo: false,
      videoSrc: '',
      processInfoDetail:{}
    }
  },
  methods:{
    getProcessInfoDetailById: function () {
      getProcessDetail({id: this.processId}, res=>{
        this.processInfoDetail=res.data
      })
    },
    previewVideo: function (item) {
      this.videoSrc = item.filePath
      this.showVideo = true
    }
  },
  created(){
    this.processId=this.$route.query.processId
    this.checkState=this.$route.query.checkState
    this.getProcessInfoDetailById()
  }
}
</script>

<style scoped>
.imgULList{
  margin: 10px;
  padding: 2px;
  float:left;
  width: 30%;
  text-align: center;
}
</style>



