<template>
  <div class="login" :style="minHeight">
    <yd-flexbox style="padding-left: 30px;padding-right: 30px;padding-top: 150px;padding-bottom: 100px;" direction="vertical">
      <yd-flexbox-item style="height: 100px;">
        <div style="text-align: center;bottom: 0px;">
          <img  src="../assets/img/shtoone.png" style=" width: 76px;height: 76px;"/>
        </div>
        <div style="text-align: center;">
          <span style="font-family: 华文楷体;font-size: 26px;">现场管理系统</span>
        </div>
      </yd-flexbox-item>
      <yd-flexbox-item align="center">
        <yd-cell-group>
          <yd-cell-item>
            <yd-icon slot="left" name="ucenter-outline" class="loginIconStyle" color="#777"></yd-icon>
            <yd-input slot="right" required type="text" v-model="user.account" placeholder="请输入用户名"></yd-input>
          </yd-cell-item>
          <yd-cell-item>
            <yd-icon slot="left" name="question" class="loginIconStyle" color="#777"></yd-icon>
            <yd-input slot="right" required type="password" v-model="user.password" placeholder="请输入密码"></yd-input>
          </yd-cell-item>
        </yd-cell-group>
        <yd-button size="large" bgcolor="#2d8cf0" shape="circle" color="#ffffff" @click.native="signIn">{{loginBtnName}}</yd-button>
        <div style="padding-top: 10px;text-align: center;">
          <span style="font-size: 14px;color: #000000;">
            版权所有©上海同望信息有限公司
          </span>
        </div>
      </yd-flexbox-item>
    </yd-flexbox>
  </div>
</template>

<script>
	import { setLoginUser, setLoginInfo, getLoginInfo } from '../assets/js/assist'
  import {login, getLoginUserInfo, isSupportOnePhone} from '../api/sysUser'
  import {initGetui} from '../api/igexinPush'
  import {messagePush} from '../assets/plus/push'
  import {loadThreeApp} from '../assets/plus/runtime'
  export default {
    name: 'login',
    data () {
      return {
        loginBtnName: '登录',
        user: {
          account: 'shtoone',
          password: 'shtoone2008-2018'
        },
        minHeight:''
      }
    },
    created() {
      this.minHeight = "min-height:" + document.body.clientHeight + "px";
      this.initLogin();
      loadThreeApp(res=>{
        console.log(JSON.stringify(res))
      })
    },
    methods: {
      initLogin(){
        let loginInfo = getLoginInfo()
        if (loginInfo) {
          this.user.account = loginInfo.account;
          this.user.password = loginInfo.password;
          this.signIn();
        }
      },
      signIn () {  //登录
        let userAccount =this.user.account;
        let password=this.user.password;
        let b64pwd = b64_md5(password)+ "==";
        b64pwd = encodeURIComponent(b64pwd);
        if ( !userAccount || !password) {
          this.$dialog.toast({
            mes: "请检查用户名或密码。",
            timeout: 1500
          });
        }else {
          this.loginBtnName = '正在登录...';
          login({username:userAccount,password:password}, res=>{
            if(res){
              setLoginUser(res);
              setLoginInfo(userAccount, password)
              this.$store.dispatch('setUserInfo',{username:userAccount,password:password})
              this.$store.dispatch('setToken', res);
              /**
              let imei;
              messagePush('getClientId', null, null, null, res=>{
                imei=res.imei;
              });
              //通过token获取当前登录对象，然后判断是否支持一人一机
              this.getLoginUserInfoByToken(imei);
               **/
              //对于浏览器测试
              this.$router.push("/tab");
            }else{
              this.$dialog.toast({
                mes: "登录失败",
                icon: 'error',
                timeout: 1500
              });
              this.loginBtnName = '登录';
            }
          })
        }
      },
      registerGetui(){  //注册个推
        messagePush('getClientId', null, null, null, res=>{
          initGetui({
            clientId: res.clientid,
            imei: res.imei,
            imsi: res.imsi,
            model: res.model,
            vendor: res.vendor,
            uuid: res.uuid,
            phoneType: res.phoneType
          }, res=>{
            if(res.ok){
              this.$router.push("/tab");
            }
          })
        })
      },
      getLoginUserInfoByToken(imei){  //判断是否一人一机
        getLoginUserInfo(res=>{
          let oneUserOnPhone=res.data.peopleOnPhone;
          if(oneUserOnPhone==='1'){
            this.judgeOnePhone(res.data.id, imei);
          }else{
            console.log('不需要一人一机的限制');
            this.registerGetui();
            this.$router.push("/tab");
          }
        })
      },
      judgeOnePhone(userid, imei){  //一人一机验证
        isSupportOnePhone({userid: userid}, res=>{
          console.log(JSON.stringify(res.data));
          if(res.data===null){
            this.$dialog.toast({
              mes: "请前往平台进行注册唯一码",
              icon: 'error',
              timeout: 1500
            });
          }else {
            if (res.data.imei === null || res.data.imei === '' || res.data.imei === undefined) {
              this.$dialog.toast({
                mes: "请前往平台进行注册唯一码",
                icon: 'error',
                timeout: 1500
              });
            } else {
              if (imei === res.data.imei) {
                console.log('一人一机验证通过');
                this.registerGetui();
                this.$router.push("/tab");
              } else {
                this.$dialog.toast({
                  mes: "请核对手机唯一码",
                  icon: 'error',
                  timeout: 1500
                });
              }
            }
          }
        });
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .login{
    /*background:url(../assets/img/bg.jpg)no-repeat center center;*/
    background-color: #FFFFFF;
    width:100%;
    height:100%;
    background-size:100% 100%;
    position:absolute;
  }
  .loginIconStyle{
    margin-right: 10px;
    color: #777;
  }
</style>



