<template>
  <div class="cs_contain has_nav_container">
    <PubNavBar title="指令转发" ></PubNavBar>
    <div class="syjc_main">
      <div class="syjc_form">
        <div class="syjc_form_item">
          <yd-cell-group>
            <yd-cell-item>
              <span slot="left">指令转发用户类型：</span>
              <select slot="right" v-model="userType" @change="selectTranspondUser()">
                <option value="">-请选择-</option>
                <option value="1">项目</option>
                <option value="2">业主</option>
                <option value="3">监理</option>
                <option value="4">标段</option>
              </select>
            </yd-cell-item>
            <yd-cell-item v-show="showSelectedPerson">
              <span slot="left">指令用户类：</span>
              <span slot="left">{{username}}</span>
            </yd-cell-item>
            <yd-cell-item v-show="showSelectedPerson">
              <span slot="left">转发描述:</span>
              <yd-textarea slot="right" v-model="transpondCommandObj.remark" placeholder="请输入转发描述" maxlength="100"></yd-textarea>
            </yd-cell-item>
            <yd-button v-show="showSelectedPerson" size="large" type="primary" shape="circle" @click.native="transpondCommandFun">提交</yd-button>
          </yd-cell-group>
        </div>
      </div>
    </div>
    <!--  监理验收人  -->
    <yd-popup v-model="isShowTransponComandCheckPerson" position="bottom" height="90%">
      <yd-infinitescroll :callback="loadTranspondCheckPersonList" ref="infinitescrollDemo">
        <yd-list theme="4" slot="list">
          <yd-cell-item v-for="(item, key) in transpondComandCheckPersonList" :key="key">
            <span slot="left">
              <yd-checkbox-group v-model="transpondCommandObj.zhidingren" :callback="selectedCheckPersons">
                <yd-checkbox :val="item.id+','+item.username" shape="circle">{{item.username}}  电话:{{item.mobilePhone}}  职位:{{item.zhiwei}}</yd-checkbox>
              </yd-checkbox-group>
            </span>
          </yd-cell-item>
        </yd-list>
        <span slot="doneTip">全部加载完成</span>
      </yd-infinitescroll>
    </yd-popup>
  </div>
</template>

<script>
import {getCommandPerson, transpondComandSubmit} from '../../../../api/command'
export default {
  name: 'transpondCommand',
  data(){
    return {
      userType: '',
      username: '',
      pageNo: 1,
      pageSize: 15,
      isShowTransponComandCheckPerson: false,
      showSelectedPerson: false,
      transpondComandCheckPersonList: [],
      transpondCommandObj:{
        remark:'',
        commanduserId: '',
        zhidingren: []
      }
    }
  },
  methods:{
    selectTranspondUser(){
      this.isShowTransponComandCheckPerson=true
      this.loadTranspondCheckPersonList()
    },
    loadTranspondCheckPersonList(){
      getCommandPerson({pageNo: this.pageNo, pageSize: this.pageSize, Mark: this.userType, name: null, work: null}, res=>{
        if(res.ok){
          if(res.data && res.data.data.length > 0){
            this.transpondComandCheckPersonList = this.transpondComandCheckPersonList.concat(res.data.data)
            /* 单次请求数据完毕 */
            this.$refs.infinitescrollDemo.$emit('ydui.infinitescroll.finishLoad');
            this.pageNo++
          }else{
            /* 所有数据加载完毕 */
            this.$refs.infinitescrollDemo.$emit('ydui.infinitescroll.loadedDone');
          }
        }
      })
    },
    selectedCheckPersons:function (e) {
      if(e.length>1){
        this.$dialog.toast({
          mes: '只能选择一个',
          timeout: 1500,
          icon: 'error'
        })
        return false
      }else if(e.length===1){
        let checkedInfo=e[0]
        this.username=checkedInfo.split(',')[1]
        this.showSelectedPerson=true
        this.isShowTransponComandCheckPerson=false
        return true
      }
    },
    transpondCommandFun:function () {
      let checkedPerson=this.transpondCommandObj.zhidingren
      if (this.commonFun.isEmptyStr(this.transpondCommandObj.remark)) {
        this.$dialog.toast({
          mes: '请填写转发描述',
          timeout: 1500
        })
        return false
      }
      if(this.transpondCommandObj.zhidingren.length <=0){
        this.$dialog.toast({
          mes: '请选择转发人',
          timeout: 1500
        })
        return false
      }
      transpondComandSubmit({commanduserId: this.transpondCommandObj.commanduserId, zhidingren: checkedPerson[0].split(',')[0], remark: this.transpondCommandObj.remark}, res=>{
        if(res.ok){
          this.$dialog.toast({
            mes: '指令转发成功',
            timeout: 1500
          })
          this.$router.push('/commandInfo')
        }else{
          this.$dialog.toast({
            mes: '指令转发失败',
            timeout: 2000,
            icon: 'error'
          })
        }
      })
    }
  },
  created(){
    this.transpondCommandObj.commanduserId=this.$route.query.commanduserId
  }
}
</script>

<style scoped>

</style>



