<template>
  <div class="cs_contain has_nav_container">
    <PubNavBar title="监理日志" :rightFun="this.$ltx.permissionJudge('createLog')" customeIcon="photo" @customeIconFun="createSupervisorLog()"></PubNavBar>
    <div class="cs_main">
      <yd-pullrefresh :callback="loadLogListPullRefresh" ref="pullrefreshDemo">
        <yd-infinitescroll :callback="loadLogList" ref="infinitescrollDemo">
          <yd-list theme="4" slot="list">
            <yd-list-item v-for="(item, key) in supervisorLogList" :key="key" @click.native="supervisorLogDetail(item.id)">
              <img slot="img" :src="item.filePath"/>
              <span slot="title">{{item.describe}}</span>
              <yd-list-other slot="other">
                <div>
                  <span class="list-price"><b>创建人:</b>{{item.realname}}</span>
                  <span class="list-del-price">{{item.createTime}}</span>
                  <br/>
                  <br/>
                  <span class="list-del-price"><yd-icon name="location" size="18px" color="#777"></yd-icon>{{item.photoLocation}}</span>
                </div>
              </yd-list-other>
            </yd-list-item>
          </yd-list>
          <!-- 数据全部加载完毕显示 -->
          <span slot="doneTip">全部加载完成</span>
        </yd-infinitescroll>
      </yd-pullrefresh>
    </div>
  </div>
</template>

<script>
import PubNavBar from '../../components/pub/NavBar'
import { getSupervisorLogList } from '../../api/supervisorLog'
export default {
  data () {
    return {
      supervisorLogList: [],
      searchData: {
        pageNo: 1,
        pageSize: 15,
        startTime: '',
        endTime: '',
        userName: ''
      }
    }
  },
  components: {
    PubNavBar: PubNavBar
  },
  methods: {
    loadLogList: function () {
      getSupervisorLogList(this.searchData, res => {
        if(res.ok) {
          if(res.data && res.data.data.length > 0){
            this.supervisorLogList = this.supervisorLogList.concat(res.data.data)
            /* 单次请求数据完毕 */
            this.$refs.infinitescrollDemo.$emit('ydui.infinitescroll.finishLoad');
            this.searchData.pageNo++
          }else{
            /* 所有数据加载完毕 */
            this.$refs.infinitescrollDemo.$emit('ydui.infinitescroll.loadedDone');
          }
        }
      })
    },
    loadLogListPullRefresh: function(){
      getSupervisorLogList(this.searchData, res => {
        if(res.ok) {
          if(res.data && res.data.data.length > 0){
            this.supervisorLogList = this.supervisorLogList.concat(res.data.data)
            /* 单次请求数据完毕 */
            this.$refs.pullrefreshDemo.$emit('ydui.pullrefresh.finishLoad');
            this.searchData.pageNo++
          }else{
            /* 所有数据加载完毕 */
            this.$dialog.toast({
              mes:  '已是最新内容'
            });
          }
        }
      })
    },
    createSupervisorLog: function () {
      this.$router.push('/createLog')
    },
    supervisorLogDetail:function (id) {
      this.$router.push('/supervisorLogDetail/' + id)
    }
  },
  created: function () {
    this.loadLogList()
  }
}
</script>

<style scoped>
  .cs_contain{
    height: 100%;
    overflow: auto;
  }
  .cs_search{
    background-color: #fff;
  }
  .yd-cell-box{
    margin-bottom: 0;
  }
  .cs_main{
    margin-bottom: 60px;
  }
</style>



