import {get, post, upload} from '../axios/ajax'

export const urls = {
  getEveryProjectItemInfoByLastId: '/projectItemInfo/getEveryProjectItemInfoByLastId',    //根据选择工程分部分项目最后一级往前寻找其上级
  selectProcessDict: '/processType/multipleSelectProcess',    //工序选择字典
  appointSelftProcess: '/processCheck/addProcess',      //自主验收---指定工序
  appointSupervisorCheckPerson: '/processCheck/getCheckPerson',   //自主验收---施工单位指定监理验收
  constructionSelfcheck: '/processCheck/selfCheck',   //自主验收
  getSendSelfCheckSearch: '/processCheck/selfCheckSearch',   //自主验收----我发起的
  getRevieveSelfCheckSearch: '/processCheck/receiveSelfCheckSearch',   //自主验收---我收到
  getProcessDetail: '/processCheck/getProcessDetail',    //得到验收详细情况查看
  getReceiveCheckTaskSearch: '/processCheck/receiveCheckTaskSearch',   //收到的验收任务
  getSendCheckTaskSearch: '/processCheck/launchCheckTaskSearch',  //发出的验收任务
  supervisionCheck: '/processCheck/supervisionCheck',    //监理验收
  searchHenJi: '/processCheck/CheckSearch',   //验收查询
  getUsersByDepartId: '/processCheck/notDeletedUser',  //根据组织机构id其下属所有用户
  appointProcessCheckPersons: '/processCheck/appointProcessCheckPersons',   //指定验收计划
  getAllCheckPlanPage: '/processCheck/myLaunchCheckSearch' //罗列当前用户自己发出的验收计划
}

/**
 * 根据选择工程分部分项目最后一级往前寻找其上级
 * @param params
 * @param callback
 */
export const getEveryProjectItemInfoByLastId = (params, callback) => { post(urls.getEveryProjectItemInfoByLastId, params, {'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'}, callback) }

/**
 * 自主验收之---指定工序
 * @param params
 * @param callback
 */
export const selfProcess_appointProcess = (params, callback) => { post(urls.appointSelftProcess, params, {'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'}, callback) }

/**
 * 自主验收时指定监理单位人员
 * @param params
 * @param callback
 */
export const appointSupervisorCheckPerson = (params, callback) => { post(urls.appointSupervisorCheckPerson, params, {'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'}, callback) }

/**
 * 发起自主验收
 * @param params
 * @param callback
 */
export const constructionSelfcheck = (params, callback) => { upload(urls.constructionSelfcheck, params, callback) }

/**
 * 我发的自主验收
 * @param params
 * @param callback
 */
export const getSendSelfCheckSearch = (params, callback) => { post(urls.getSendSelfCheckSearch, params, {'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'}, callback) }

/**
 * 我收到的自主验收
 * @param params
 * @param callback
 */
export const getRevieveSelfCheckSearch = (params, callback) => { post(urls.getRevieveSelfCheckSearch, params, {'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'}, callback) }

/**
 * 工序验收详情查看
 * @param params
 * @param callback
 */
export const getProcessDetail = (params, callback) => { post(urls.getProcessDetail, params, {'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'}, callback) }

/**
 * 验收任务查询---收到
 * @param params
 * @param callback
 */
export const getReceiveCheckTaskSearch = (params, callback) => { post(urls.getReceiveCheckTaskSearch, params, {'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'}, callback) }

/**
 * 验收任务查询---发出
 * @param params
 * @param callback
 */
export const getSendCheckTaskSearch = (params, callback) => { post(urls.getSendCheckTaskSearch, params, {'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'}, callback) }

/**
 * 监理验收
 * @param params
 * @param callback
 */
export const supervisionCheck = (params, callback) => { upload(urls.supervisionCheck, params, callback)}

/**
 * 验收查询
 * @param params
 * @param callback
 */
export const getCheckSearchPage = (params, callback) => { post(urls.searchHenJi, params, {'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'}, callback) }

/**
 * 工序字典
 * @param params
 * @param callback
 */
export const selectProcessDict = (params, callback) => { get(urls.selectProcessDict, params, {'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'}, callback) }

/**
 * 根据组织机构id获取其下属所有的用户
 * @param params
 * @param callback
 */
export const getNoDelUsersByDepartId = (params, callback) => { post(urls.getUsersByDepartId, params, {'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'}, callback) }

/***
 * 设置验收计划
 * @param params
 * @param callback
 */
export const setPlanCheckPerson = (params, callback) => { post(urls.appointProcessCheckPersons, params, {'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'}, callback) }

/***
 * 罗列所有计划
 * @param params
 * @param callback
 */
export const getAllCheckPlanPage = (params, callback) => { post(urls.getAllCheckPlanPage, params, {'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'}, callback) }



