<template>
  <div class="cs_contain has_nav_container">
    <PubNavBarTo title="指令查询" gotoVueName="/tab" ></PubNavBarTo>
    <div class="cs_search">
      <yd-flexbox>
        <yd-flexbox-item>
          <yd-cell-group>
            <yd-cell-item>
              <span slot="left">组织机构：</span>
              <yd-input slot="right" v-model="showName.orgName" placeholder="请选择组织机构" :readonly="true" @click.native="selectUserGroup"></yd-input>
            </yd-cell-item>
            <yd-cell-item>
              <span slot="left">分部分项：</span>
              <yd-input slot="right" v-model="showName.projectItemName" placeholder="请选择分部分项" :readonly="true" @click.native="selectProjectItem"></yd-input>
            </yd-cell-item>
          </yd-cell-group>
        </yd-flexbox-item>
        <div style="width: 60px;text-align: center;">
          <div class="huixin_search_btn" @click="search()">搜索</div>
        </div>
      </yd-flexbox>
    </div>
    <div class="zlxj_main">
      <yd-infinitescroll :callback="loadCommandList" ref="infinitescrollCommand">
        <div class="zlxj_list" slot="list">
          <div class="zlxj_item" v-for="(item, index) in commandList" :key="index" @click="findCommandDetail(item.id,'receive', item.issolve)">
            <div class="zlxj_item_left">
              <div class="zlxj_item_left">
                <div class="zlxj_item_remark">
                  <div>{{item.remark}}</div>
                  <span class="zlxj_item_remark_span" :class="'zlxj_item_remark_span'+item.commandType">
                    <template v-if="item.commandType==='1'">安全</template>
                    <template v-else-if="item.commandType==='2'">纸质</template>
                    <template v-else-if="item.commandType==='3'">口头</template>
                  </span>
                </div>
                <div class="zlxj_item_fbfx">创建时间: {{item.createTime}}</div>
                <div class="zlxj_item_bot">
                  <div class="zlxj_item_time">
                    <div>{{item.project}}</div>
                    <span>创建人：{{item.initiator}}</span>
                  </div>
                  <div class="zlxj_item_pro">
                    <span>完成时间：{{item.planTime}}</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <!-- 数据全部加载完毕显示 -->
        <span slot="doneTip">全部加载完成</span>
      </yd-infinitescroll>
    </div>
    <!-- 弹框中的组织机构 -->
    <yd-popup v-model="isShowUserGroup" position="center" width="90%">
      <vue-ztree :list.sync='userGroup' :func="ztreeNodeClick" :is-open='true'></vue-ztree>
    </yd-popup>
    <!-- 弹框中的工程分部分项 -->
    <yd-popup v-model="isShowProjectItem" position="center" width="90%">
      <vue-ztree :list.sync='projectItem' :func="ztreeProjectItemNodeClick" :is-open='true'></vue-ztree>
    </yd-popup>
  </div>
</template>

<script>
import PubNavBarTo from '../../../components/pub/NavBarTo'
import {getUserDepartZtree, getProjectItemListByOrgId} from '../../../api/polling'
import {getCommandList} from '../../../api/command'
export default {
  name: "commandSearch",
  data(){
    return{
      isShowProjectItem: false,
      isShowUserGroup: false,
      projectItem: [],
      userGroup: [],
      commandList: [],
      showName: {
        orgName: '',
        projectItemName: ''
      },
      showValue:{
        departId:'',
        projectItemId: '',
        pageNo: 1,
        pageSize: 15,
        starttime: '',
        endtime: ''
      }
    }
  },
  components:{
    PubNavBarTo: PubNavBarTo
  },
  methods:{
    selectUserGroup: function () {
      this.isShowUserGroup = true
      this.initUserGroupZtree()
    },
    initUserGroupZtree: function () {
      getUserDepartZtree(res => {
        this.userGroup = res.data
      })
    },
    ztreeNodeClick: function (m) {
      m.hover = false
      let node = JSON.parse(JSON.stringify(m))
      if (node.orgTpye === '4') {
        this.orgName = m.name
        this.orgId = m.id
        this.isShowUserGroup = false
      } else {
        this.$dialog.toast({
          mes: '施工单位下才有分部分项呢',
          timeout: 1500
        })
      }
    },
    selectProjectItem: function () {
      this.isShowProjectItem = true
      this.initProjectItemZtree(this.orgId)
    },
    ztreeProjectItemNodeClick: function (m) {
      m.hover = false
      this.projectItemName = m.name
      this.searchInfo.projectItemCode = m.projectCode
      this.isShowProjectItem = false
    },
    initProjectItemZtree: function (orgId) {
      getProjectItemListByOrgId({orgId: orgId}, res=>{
        this.projectItem = res.data
      })
    },
    search:function () {
      this.commandList = []
      this.showValue.pageNo = 1
      this.$refs.infinitescrollCommand.$emit('ydui.infinitescroll.reInit')
      this.loadCommandList()
    },
    loadCommandList:function(){
      getCommandList(this.showValue, res=>{
        if(res.ok) {
          if(res.data && res.data.data.length > 0){
            this.commandList = this.commandList.concat(res.data.data)
            /* 单次请求数据完毕 */
            this.$refs.infinitescrollCommand.$emit('ydui.infinitescroll.finishLoad');
            this.showValue.pageNo++
          }else{
            /* 所有数据加载完毕 */
            this.$refs.infinitescrollCommand.$emit('ydui.infinitescroll.loadedDone');
          }
        }
      })
    },
    findCommandDetail:function (id, state, issolve) {
      this.$router.push({
        path: '/commandDetail',
        query: {
          commandId: id,
          tabState: state,
          issolve: '1'
        }
      })
    }
  },
  created(){
    this.loadCommandList()
  }
}
</script>

<style scoped>
.zlxj_item{
  border-bottom: 1px solid #eee;
  background-color: #fff;
  padding: 5px 10px;
  box-sizing: border-box;
  margin: 8px 5px;
  border-radius: 3px;
  box-shadow: 2px 2px 5px #dadada;
}
.zlxj_item_remark{
  line-height: 20px;
  font-size: 14px;
  box-sizing: border-box;
  width: 100%;
  border-bottom: 1px solid #eee;
  position: relative;
}
.zlxj_item_remark div{
  overflow: hidden !important;
  text-overflow: ellipsis !important;
  white-space: nowrap !important;
  text-shadow: 0 0 0.1px #333;
  padding-right: 10px;
  color: #333;
  width: 100% !important;
}
.zlxj_item_remark_span{
  position: absolute;
  top: 0;
  right: 0;
  color: orange;
}
.zlxj_item_fbfx{
  padding: 5px 0;
  line-height: 20px;
  color: #666;
  font-size: 13px;
}
.zlxj_item_bot{
  display: flex;
}
.zlxj_item_time{
  flex: 1;
  line-height: 18px;
  font-size: 12px;
  color: #999;
  overflow: hidden !important;
  text-overflow: ellipsis !important;
  white-space: nowrap !important;
}
.zlxj_item_time label{
  margin-right: 5px;
}
.zlxj_item_pro{
  flex: 1;
  color: #999;
  line-height: 18px;
  text-align: right;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
</style>



