<template>
  <div class="has_nav_container">
    <PubNavBarTo title="指定验收之指定工序" gotoVueName="/pollingTabs"></PubNavBarTo>
    <div class="syjc_main">
      <div class="syjc_form">
        <div class="syjc_form_item">
          <yd-cell-group>
            <yd-cell-item>
              <span slot="left">组织机构</span>
              <span slot="right">
                <yd-input v-model="processShowValue.orgName" placeholder="请选择组织机构" :readonly="true" @click.native="selectUserGroup"></yd-input>
              </span>
            </yd-cell-item>
            <yd-cell-item v-show="showModel.projectItemSelectTree">
              <span slot="left">分部分项</span>
              <span slot="right">
                <yd-input v-model="processShowValue.projectItemName" placeholder="请选择分部分项" :readonly="true" @click.native="selectProjectItem"></yd-input>
              </span>
            </yd-cell-item>
            <yd-cell-item v-show="showModel.projectItemSelectDetail">
              <span slot="left">所属工程:</span>
              <span slot="right">{{processShowValue.projectItemDetailInfo}}</span>
            </yd-cell-item>
            <yd-cell-item v-show="showModel.projectItemSelectDetail">
              <span slot="left">桩号:</span>
              <span slot="right">{{processShowValue.station}}</span>
            </yd-cell-item>
            <yd-cell-item v-show="showModel.projectItemSelectDetail">
              <span slot="left">工序类型:</span>
              <input slot="right" type="text" @click.stop="showProcessType" v-model="processShowValue.process" readonly placeholder="请选择工序类型">
            </yd-cell-item>
            <yd-cell-item v-show="showModel.projectItemSelectDetail">
              <span slot="left">工序描述:</span>
              <yd-textarea slot="right" v-model="processInfo.remark" placeholder="请输入工序描述" maxlength="100"></yd-textarea>
            </yd-cell-item>
          </yd-cell-group>
        </div>
        <div class="syjc_bot">
          <yd-button v-show="showModel.projectItemSelectDetail" size="large" type="primary" shape="circle" @click.native="setSelfCheck">下一步</yd-button>
        </div>
      </div>
    </div>
    <!-- 弹框中的组织机构 -->
    <yd-popup v-model="isShowUserGroup" position="center" width="90%">
      <vue-ztree :list.sync='userGroup' :func="ztreeNodeClick" :is-open='true'></vue-ztree>
    </yd-popup>
    <!-- 弹框中的工程分部分项 -->
    <yd-popup v-model="isShowProjectItem" position="center" width="90%">
      <vue-ztree :list.sync='projectItem' :func="ztreeProjectItemNodeClick" :is-open='true'></vue-ztree>
    </yd-popup>
    <!--  工序类型/工序 -->
    <yd-popup v-model="isShowProcess" position="center" width="90%">
      <vue-ztree :list.sync='processTypeDict' :func="ztreeProcessTypeNodeClick" :is-open='false' ></vue-ztree>
    </yd-popup>
  </div>
</template>

<script>
import PubNavBarTo from '../../../components/pub/NavBarTo'
import {getUserDepartZtree, getProjectItemListByOrgId} from '../../../api/polling'
import {getEveryProjectItemInfoByLastId, selfProcess_appointProcess, selectProcessDict} from '../../../api/processCheck'
export default {
  data () {
    return {
      isShowUserGroup: false,
      isShowProjectItem: false,
      isShowProcess: false,
      showModel: {
        projectItemSelectTree: false,
        projectItemSelectDetail: false,
        processDetail: false
      },
      userGroup: [],
      projectItem: [],
      processTypeDict: [],
      processShowValue: {
        orgName: '',
        projectItemName: '',
        projectItemDetailInfo: '',
        station: '',
        process: ''
      },
      selectedUserGroup: '',  //选中的组织机构对象
      processInfo: {
        orgId: '',
        projectItemId: '',
        processTypeId: '',
        processId: '',
        remark: ''
      },
    }
  },
  components:{
    PubNavBarTo: PubNavBarTo
  },
  mounted:function(){

  },
  methods: {
    selectUserGroup: function () {
      this.isShowUserGroup = true
      this.initUserGroupZtree()
    },
    initUserGroupZtree: function () {
      getUserDepartZtree(res => {
        this.userGroup = res.data
      })
    },
    ztreeNodeClick: function (m) {
      m.hover = false
      let node = JSON.parse(JSON.stringify(m))
      if (node.children.length === 0) {
        this.processShowValue.orgName = m.name
        this.processInfo.orgId = m.id
        this.selectedUserGroup=m;  //设置选中的组织机构id
        this.isShowUserGroup = false
        this.showModel.projectItemSelectTree = true
      } else {
        this.$dialog.toast({
          mes: '施工单位下才有分部分项呢',
          timeout: 1500
        })
      }
    },
    selectProjectItem: function () {
      this.isShowProjectItem = true
      this.initProjectItemZtree(this.processInfo.orgId)
    },
    ztreeProjectItemNodeClick: function (m) {
      m.hover = false
      let node = JSON.parse(JSON.stringify(m))
      if (node.children.length === 0) {
        this.processShowValue.projectItemName = m.name
        this.processInfo.projectItemId = m.id
        this.isShowProjectItem = false
        this.loadProjectItemDetail(m.id)
        this.showModel.projectItemSelectDetail = true
      } else {
        this.$dialog.toast({
          mes: '请选择子项工程分部分项',
          timeout: 1500,
          icon: 'error'
        })
      }
    },
    initProjectItemZtree: function (orgId) {
      getProjectItemListByOrgId({orgId: orgId}, res=>{
        this.projectItem = res.data
      })
    },
    loadProjectItemDetail: function (projectItemId) {
      getEveryProjectItemInfoByLastId({projectId: projectItemId}, res=>{
        if(res.ok) {
          this.processShowValue.projectItemDetailInfo = res.data.projectItem
          this.processShowValue.station = res.data.station
        }
      })
    },
    setSelfCheck: function () {
      if (this.commonFun.isEmptyStr(this.processInfo.remark)) {
        this.$dialog.toast({
          mes: '请填写工序描述',
          timeout: 1500
        })
        return false
      }
      if (this.commonFun.isEmptyStr(this.processInfo.processId)) {
        this.$dialog.toast({
          mes: '请选择工序',
          timeout: 1500
        })
        return false
      }
      selfProcess_appointProcess({
        userGroupId: this.processInfo.orgId,
        processMDictId: this.processInfo.processTypeId,
        processDictId: this.processInfo.processId,
        projectItemId: this.processInfo.projectItemId,
        remark: this.processInfo.remark,
        checkType: '0',
        checkNum: 1
      }, res=>{
        if(res.ok) {
          this.$dialog.toast({
            mes: '指定验收之指定工序成功',
            timeout: 1500
          })
          this.$router.push({
            path: '/selectCheckPerson',
            query: {
              processMainId: res.data,
              userGroupId: this.selectedUserGroup.id,
              parentUserGroupId: this.selectedUserGroup.parentdepartid
            }
          })
        }else{
          this.$dialog.toast({
            mes: '指定验收之指定工序失败',
            timeout: 2000,
            icon: 'error'
          })
        }
      })
    },
    showProcessType:function(){
      this.isShowProcess=true;
      this.initProcessTypeZtree();
    },
    initProcessTypeZtree: function () {
      selectProcessDict(null, res=>{
        this.processTypeDict = res.data
      })
    },
    ztreeProcessTypeNodeClick: function (node, parent, trees) {
      node.hover = false
      this.processInfo.processTypeId = parent.id
      this.processInfo.processId = node.id
      this.processShowValue.process = parent.name + '>' + node.name;
      this.isShowProcess=false;
    }
  }
}
</script>

<style scoped>

</style>



