<template>
	<div class="nb_main">
		<yd-navbar :title="title" :fixed="true" height="45px" fontsize="14px" color="#fff" bgcolor="#2B79ED" border-color="#2B79ED">
			<template slot="left" v-if="back">
				<yd-navbar-back-icon color="#fff" @click.native="$router.go(-1)"><label style="display: inline-block;vertical-align: middle;margin-top: -1px;color: #fff;font-size: 14px;">{{backTitle}}</label></yd-navbar-back-icon>
			</template>
			<template slot="center">
				<slot name="center"></slot>
			</template>
			<template slot="right" v-if="rightFun">
        <yd-icon slot="icon" size="24px" :name="customeIcon" :color="customeIconColor" custom @click.native="customeIconFun"></yd-icon>
			</template>
		</yd-navbar>
	</div>
</template>

<script>
	export default {
		data () {
			return {

			}
		},
		props: {
			title: {
				type: String
			},
			backTitle: {
				type: String,
				default: '返回'
			},
			back: {
				type: Boolean,
				default: true
			},
      rightFun: {
			  type: Boolean,
        default: false
      },
      customeIcon: {
			  type: String,
        default: ''
      },
      customeIconColor: {
        type: String,
        default: '#fff'
      }
		},
		created() {

		},
		methods: {
			goback(){
				this.$router.go(-1)
			},
      customeIconFun(){
        this.$emit('customeIconFun')
      }
		}
	}
</script>

<style scoped>
	.yd-navbar-fixed{
		background: -webkit-linear-gradient(to right, #0B3AC8 , #2B79ED);
		background: -o-linear-gradient(to right, #0B3AC8 , #2B79ED);
		background: -moz-linear-gradient(to right, #0B3AC8 , #2B79ED);
		background: linear-gradient(to right, #0B3AC8 , #2B79ED) ;
	}
</style>



