<template>
  <div class="cs_contain has_nav_container">
    <PubNavBar title="创建指令"></PubNavBar>
    <div class="syjc_main">
      <div class="syjc_form">
        <div class="syjc_form_item">
          <yd-cell-group>
            <yd-cell-item>
              <span slot="left">组织机构：</span>
              <yd-input slot="right" v-model="commandShowValue.orgName" placeholder="请选择组织机构" :readonly="true" @click.native="selectUserGroup"></yd-input>
            </yd-cell-item>
            <yd-cell-item v-show="showModel.projectItemSelectTree">
              <span slot="left">分部分项：</span>
              <yd-input slot="right" v-model="commandShowValue.projectItemName" placeholder="请选择分部分项" :readonly="true" @click.native="selectProjectItem"></yd-input>
            </yd-cell-item>
            <yd-cell-item v-show="showModel.projectItemSelectDetail">
              <span slot="left">所属工程：</span>
              <span slot="right">{{commandShowValue.projectItemDetailInfo}}</span>
            </yd-cell-item>
            <yd-cell-item v-show="showModel.projectItemSelectDetail">
              <span slot="left">桩号：</span>
              <span slot="right">{{commandShowValue.station}}</span>
            </yd-cell-item>
            <yd-cell-item v-show="showModel.projectItemSelectDetail">
              <span slot="left">工序类型：</span>
              <input slot="right" type="text" @click.stop="showProcessType" v-model="commandShowValue.process" readonly placeholder="请选择工序类型">
            </yd-cell-item>
            <yd-cell-item v-show="showModel.commandDetail">
              <span slot="left">计划检查时间：</span>
              <yd-datetime type="date" v-model="commandInfo.planCheckTime" :start-date="defaultPlanCheckTime" slot="right"></yd-datetime>
            </yd-cell-item>
            <yd-cell-item v-show="showModel.commandDetail">
              <span slot="left">接收人所在单位：</span>
              <yd-input slot="right" v-model="commandShowValue.receiveOrgName" placeholder="请选择接收人所在单位" :readonly="true" @click.native="selectReceiveUserGroup"></yd-input>
            </yd-cell-item>
            <yd-cell-item v-show="showModel.commandDetail">
              <span slot="left">接收人：</span>
              <yd-input slot="right" v-model="commandShowValue.planCheckPerson" placeholder="请选择接收人" :readonly="true" @click.native="selectPlanCheckPerson"></yd-input>
            </yd-cell-item>
            <yd-cell-item v-show="showModel.commandDetail">
              <span slot="left">指令描述:</span>
              <yd-textarea slot="right" v-model="commandInfo.remark" placeholder="请输入指令描述" maxlength="100"></yd-textarea>
            </yd-cell-item>
          </yd-cell-group>
        </div>
        <div class="syjc_att">
          <Attach :attachList="commandInfo.commandFileList" :delAttachList="commandInfo.delCommandFileList" :readonly="showModel.attachReadonly" :sourceType="2"></Attach>
        </div>
        <div class="syjc_bot">
          <yd-button v-show="showModel.commandDetail" size="large" type="primary" shape="circle" @click.native="subimtCommand">发送</yd-button>
        </div>
      </div>
    </div>
    <!-- 弹框中的组织机构 -->
    <yd-popup v-model="isShowUserGroup" position="center" width="90%">
      <vue-ztree :list.sync='userGroup' :func="ztreeNodeClick" :is-open='true'></vue-ztree>
    </yd-popup>
    <!-- 弹框中的工程分部分项 -->
    <yd-popup v-model="isShowProjectItem" position="center" width="90%">
      <vue-ztree :list.sync='projectItem' :func="ztreeProjectItemNodeClick" :is-open='true'></vue-ztree>
    </yd-popup>
    <!--  工序类型/工序 -->
    <yd-popup v-model="isShowProcess" position="center" width="90%">
      <vue-ztree :list.sync='processTypeDict' :func="ztreeProcessTypeNodeClick" :is-open='false' ></vue-ztree>
    </yd-popup>

    <!-- 指令接收人筛选 -->
    <yd-popup v-model="isReceiveShowUserGroup" position="center" width="90%">
      <vue-ztree :list.sync='receiveUserGroup' :func="ztreeReceiveNodeClick" :is-open='true'></vue-ztree>
    </yd-popup>
    <!--  指令接收人  -->
    <yd-popup v-model="showModel.isShowPlanCheckPerson" position="bottom" height="90%">
      <yd-infinitescroll :callback="loadcheckPersonList" ref="infinitescrollDemo">
        <yd-list theme="4" slot="list">
          <yd-list-item v-for="(item, key) in planCheckPersonList" :key="key" style="height: 45px;">
            <div slot="img" style=" margin-top: -50px; margin-left: 10px;">
              <yd-checkbox-group v-model="commandInfo.planCheckPersonId" :callback="selectedCheckPersons">
                <yd-checkbox :val="item.id+','+item.username" shape="circle">{{item.username}}</yd-checkbox>
              </yd-checkbox-group>
            </div>
            <div slot="other">
              <span style="font-weight: bold;">电话:</span>{{item.mobilePhone}}
              <br/>
              <span style="font-weight: bold;">职位:</span>{{item.zhiwei}}
            </div>
          </yd-list-item>
        </yd-list>
        <span slot="doneTip">全部加载完成</span>
      </yd-infinitescroll>
    </yd-popup>
  </div>
</template>

<script>
import PubNavBar from '../../../components/pub/NavBar'
import Attach from '../../../components/pub/Attach'
import {formatDate} from '../../../assets/js/assist'
import { getGeolocation } from '../../../assets/plus/geolocation'
import {getUserDepartZtree, getProjectItemListByOrgId} from '../../../api/polling'
import {getEveryProjectItemInfoByLastId, selectProcessDict, getNoDelUsersByDepartId} from '../../../api/processCheck'
import {sendCommand} from '../../../api/command'
export default {
  name: "createCommand",
  data(){
    return{
      userGroup:[],
      receiveUserGroup: [],   //接收人的组织机构
      projectItem: [],
      planCheckPersonList: [],
      processTypeDict: [],
      isShowUserGroup: false,
      isShowProjectItem: false,
      isReceiveShowUserGroup: false,  //接收人
      isShowProcess: false,
      defaultPlanCheckTime: '',
      pageNo:1,
      pageSize: 15,
      showModel:{
        projectItemSelectTree: false,
        attachReadonly: true,
        projectItemSelectDetail: false,
        processDetail: false,
        commandDetail: false,
        isShowPlanCheckPerson: false
      },
      commandShowValue:{
        orgName: '',
        receiveOrgName: '',  //接收人所在组织机构
        projectItemName: '',
        projectItemDetailInfo: '',
        station:'',
        process: '',
        planCheckPerson:'',
        selectedReceiveId: ''  //选中的接收人所在的组织机构
      },
      commandInfo:{
        commandFileList:[],
        delCommandFileList:[],
        remark: '',
        orgId: '',
        projectItemId: '',
        processTypeId: '',
        processId: '',
        ReceiveUserid: '',
        planCheckTime: '',
        planCheckPersonId: [],
        commandType: '',
        lng: '',
        lat: '',
        photoLocation: '',
      }
    }
  },
  props: {
    pollingId: {
      type: String
    }
  },
  components:{
    PubNavBar: PubNavBar,
    Attach: Attach
  },
  methods:{
    loadLocation(){
      getGeolocation(res => {
        this.commandInfo.photoLocation = res.addresses
        this.commandInfo.lng = res.coords.longitude
        this.commandInfo.lat = res.coords.latitude
      })
    },
    selectUserGroup: function () {
      this.isShowUserGroup = true
      this.initUserGroupZtree()
    },
    selectReceiveUserGroup(){  //指令接收人组织机构
      this.isReceiveShowUserGroup=true;
      this.initUserGroupZtree(0);
    },
    initUserGroupZtree: function (type) {
      getUserDepartZtree(res => {
        if(type===0){
          this.receiveUserGroup = res.data
        }else {
          this.userGroup = res.data
        }
      })
    },
    ztreeNodeClick: function (m) {
      m.hover = false
      let node = JSON.parse(JSON.stringify(m))
      if (node.children.length==0) {
        this.commandShowValue.orgName = m.name
        this.commandInfo.orgId = m.id
        this.isShowUserGroup = false
        this.showModel.projectItemSelectTree = true
      } else {
        this.$dialog.toast({
          mes: '施工单位下才有分部分项呢',
          timeout: 1500
        })
      }
    },
    ztreeReceiveNodeClick: function (m) {
      this.commandShowValue.receiveOrgName=m.name;
      this.commandShowValue.selectedReceiveId=m.id;
      this.isReceiveShowUserGroup=false;
    },
    selectProjectItem: function () {
      this.isShowProjectItem = true
      this.initProjectItemZtree(this.commandInfo.orgId)
    },
    ztreeProjectItemNodeClick: function (m) {
      m.hover = false
      let node = JSON.parse(JSON.stringify(m))
      if (node.children.length === 0) {
        this.commandShowValue.projectItemName = m.name
        this.commandInfo.projectItemId = m.id
        this.isShowProjectItem = false
        this.loadProjectItemDetail(m.id)
        this.showModel.projectItemSelectDetail = true
      } else {
        this.$dialog.toast({
          mes: '请选择子项工程分部分项',
          timeout: 1500,
          icon: 'error'
        })
      }
    },
    initProjectItemZtree: function (orgId) {
      getProjectItemListByOrgId({orgId: orgId}, res=>{
        this.projectItem = res.data
      })
    },
    loadProjectItemDetail: function (projectItemId) {
      getEveryProjectItemInfoByLastId({projectId: projectItemId}, res=>{
        if(res.ok) {
          this.commandShowValue.projectItemDetailInfo = res.data.projectItem
          this.commandShowValue.station = res.data.station
        }
      })
    },
    selectPlanCheckPerson: function () {
      this.showModel.isShowPlanCheckPerson = true
      this.pageNo=1;
      this.loadcheckPersonList()
    },
    loadcheckPersonList: function () {
      this.$refs.infinitescrollDemo.$emit('ydui.infinitescroll.reInit');
      getNoDelUsersByDepartId({pageNo: this.pageNo, pageSize: this.pageSize, realname: null, position: null, userGroupId: this.commandShowValue.selectedReceiveId}, res=>{
        if(res.ok){
          if(res.data && res.data.data.length > 0){
            const _list = res.data.data;
            this.planCheckPersonList = [..._list, ...this.planCheckPersonList];
            /* 单次请求数据完毕 */
            this.$refs.infinitescrollDemo.$emit('ydui.infinitescroll.finishLoad');
            this.pageNo++
          }else{
            /* 所有数据加载完毕 */
            this.$refs.infinitescrollDemo.$emit('ydui.infinitescroll.loadedDone');
          }
        }
      })
    },
    selectedCheckPersons (e) {
      if(e.length>1){
        this.$dialog.toast({
          mes: '只能选择一个',
          timeout: 1500,
          icon: 'error'
        })
        return false
      }else if(e.length===1){
        let checkedInfo=e[0]
        this.commandShowValue.planCheckPerson=checkedInfo.split(',')[1]
        this.showModel.isShowPlanCheckPerson=false
        return true
      }
    },
    showProcessType: function(){
      this.isShowProcess=true
      this.initProcessTypeZtree();
    },
    initProcessTypeZtree: function () {
      selectProcessDict(null, res=>{
        this.processTypeDict = res.data
      })
    },
    ztreeProcessTypeNodeClick: function (node, parent, trees) {
      node.hover = false
      this.commandInfo.processTypeId = parent.id
      this.commandInfo.processId = node.id
      this.commandShowValue.process = parent.name + '>' + node.name
      this.showModel.commandDetail=true
      this.showModel.attachReadonly=false
      this.isShowProcess=false
    },
    subimtCommand:function () {
      let checkedPerson=this.commandInfo.planCheckPersonId
      if (this.commonFun.isEmptyStr(this.commandInfo.remark)) {
        this.$dialog.toast({
          mes: '请填写指令描述',
          timeout: 1500
        })
        return false
      }
      if (this.commandInfo.commandFileList.length <= 0) {
        this.$dialog.toast({
          mes: '请选择留底图片',
          timeout: 1500
        })
        return false
      }
      if(this.commandInfo.planCheckPersonId.length <=0){
        this.$dialog.toast({
          mes: '请选择指令接收人',
          timeout: 1500
        })
        return false
      }
      let formData = new FormData()
      formData.append('userGroupId', this.commandInfo.orgId)
      formData.append('processDictId', this.commandInfo.processId)
      formData.append('projectItemId', this.commandInfo.projectItemId)
      formData.append('ReceiveUserid', checkedPerson[0].split(',')[0])
      formData.append('planCheckTime', this.commandInfo.planCheckTime)
      formData.append('remark', this.commandInfo.remark)
      formData.append('commandType', this.commandInfo.commandType)
      formData.append('batchNo', this.commonFun.UUID())
      formData.append('lgt', this.commandInfo.lng)
      formData.append('lat', this.commandInfo.lat)
      formData.append('photoLocation', this.commandInfo.photoLocation)
      if(this.pollingId===0) {
        formData.append('patrolId', '')
      }else{
        formData.append('patrolId', this.pollingId)
      }
      for (let resourece of this.commandInfo.commandFileList) {
        if (resourece.isNew) {
          formData.append('files', resourece.file)
        }
      }
      sendCommand(formData,res=>{
        if(res.ok){
          this.$dialog.toast({
            mes: '发送指令成功',
            timeout: 1500
          })
          this.$router.push('/sendCommand')
        }else{
          this.$dialog.toast({
            mes: '发送指令失败',
            timeout: 2000,
            icon: 'error'
          })
        }
      })
    }
  },
  created(){
    this.defaultPlanCheckTime = formatDate((new Date()))
    this.loadLocation()
  }
}
</script>

<style scoped>

</style>



