import router from '../../router'
import store from '../../store'
import {getLoginToken} from './assist'
import {Loading } from 'vue-ydui/dist/lib.rem/dialog';

router.beforeEach((to, from , next) =>{
  let getTokenVal= getLoginToken()
  if(getTokenVal==='' || getTokenVal===null || getTokenVal===undefined){
    next();
  }else{
    Loading.open('数据正在加载中...');
    if(to.path==='/login'){
      next({ path: '/' });
    }else if(to.path==='/tab'){
      store.dispatch('getPermissionButton').then(res=>{
        next();
      });
    }else{
      next();
    }
  }
});

router.afterEach(() => {
  Loading.close();
})



