<template>
  <div class="has_nav_container">
    <PubNavBar title="验收查询" gotoVueName="/processTab"></PubNavBar>
    <div class="cs_search">
      <yd-flexbox>
        <yd-flexbox-item>
          <yd-cell-group>
            <yd-cell-item>
              <span slot="left">组织机构：</span>
              <yd-input slot="right" v-model="orgName" placeholder="请选择组织机构" :readonly="true" @click.native="selectUserGroup"></yd-input>
            </yd-cell-item>
            <yd-cell-item>
              <span slot="left">分部分项：</span>
              <yd-input slot="right" v-model="projectItemName" placeholder="请选择分部分项" :readonly="true" @click.native="selectProjectItem"></yd-input>
            </yd-cell-item>
          </yd-cell-group>
        </yd-flexbox-item>
        <div style="width: 60px;text-align: center;">
          <div class="huixin_search_btn" @click="search()">搜索</div>
        </div>
      </yd-flexbox>
    </div>
    <div class="zlxj_main">
      <yd-infinitescroll :callback="loadprocessSearchList" ref="infinitescrollSearch">
        <yd-list theme="4" slot="list">
          <yd-list-item v-for="(item, key) in processSearchList" :key="key" @click.native="findCheckSearchDetail(item,'search')">
            <img slot="img" :src="item.filePath"/>
            <span slot="title">{{item.processName}}</span>
            <yd-list-other slot="other">
              <ul>
                <li class="list-price"><b>计划自检人:</b>{{item.planSelfCheckPerson}}   {{item.planSelfCheckTime}}</li>
                <li class="list-price"><b>计划验收人:</b>{{item.planCheckPerson}}  {{item.planCheckTime}}</li>
                <li class="list-price"><b>验收描述:</b>{{item.checkDescribe}}</li>
                <li class="list-del-price"><b>位置:</b><yd-icon name="location" size="18px" color="#777"></yd-icon>{{item.photoLocation}}</li>
              </ul>
            </yd-list-other>
          </yd-list-item>
        </yd-list>
        <!-- 数据全部加载完毕显示 -->
        <span slot="doneTip">全部加载完成</span>
      </yd-infinitescroll>
    </div>
    <!-- 弹框中的组织机构 -->
    <yd-popup v-model="isShowUserGroup" position="center" width="90%">
      <vue-ztree :list.sync='userGroup' :func="ztreeNodeClick" :is-open='true'></vue-ztree>
    </yd-popup>
    <!-- 弹框中的工程分部分项 -->
    <yd-popup v-model="isShowProjectItem" position="center" width="90%">
      <vue-ztree :list.sync='projectItem' :func="ztreeProjectItemNodeClick" :is-open='true'></vue-ztree>
    </yd-popup>
  </div>

</template>

<script>
import PubNavBarTo from '../../../components/pub/NavBarTo'
import {getCheckSearchPage} from '../../../api/processCheck'
import {getUserDepartZtree, getProjectItemListByOrgId} from '../../../api/polling'
export default {
  name: 'processSearch',
  data(){
    return {
      projectItemName: '',
      orgName: '',
      orgId: '',
      isShowProjectItem: false,
      isShowUserGroup: false,
      processSearchList: [],
      projectItem: [],
      userGroup: [],
      searchInfo: {
        pageNo: 1,
        pageSize: 15,
        projectItemCode: '',
        processDictId: '',
        state: ''
      }
    }
  },
  components: {
    PubNavBarTo: PubNavBarTo
  },
  methods:{
    selectUserGroup: function () {
      this.isShowUserGroup = true
      this.initUserGroupZtree()
    },
    initUserGroupZtree: function () {
      getUserDepartZtree(res => {
        this.userGroup = res.data
      })
    },
    ztreeNodeClick: function (m) {
      m.hover = false
      let node = JSON.parse(JSON.stringify(m))
      if (node.orgTpye === '4') {
        this.orgName = m.name
        this.orgId = m.id
        this.isShowUserGroup = false
      } else {
        this.$dialog.toast({
          mes: '施工单位下才有分部分项呢',
          timeout: 1500
        })
      }
    },
    selectProjectItem: function () {
      this.isShowProjectItem = true
      this.initProjectItemZtree(this.orgId)
    },
    ztreeProjectItemNodeClick: function (m) {
      m.hover = false
      this.projectItemName = m.name
      this.searchInfo.projectItemCode = m.projectCode
      this.isShowProjectItem = false
    },
    initProjectItemZtree: function (orgId) {
      getProjectItemListByOrgId({orgId: orgId}, res=>{
        this.projectItem = res.data
      })
    },
    loadprocessSearchList: function () {
      getCheckSearchPage(this.searchInfo, res=>{
        if(res.ok){
          if(res.data && res.data.data.length > 0){
            this.processSearchList = this.processSearchList.concat(res.data.data)
            /* 单次请求数据完毕 */
            this.$refs.infinitescrollSearch.$emit('ydui.infinitescroll.finishLoad');
            this.searchInfo.pageNo++
          }else{
            /* 所有数据加载完毕 */
            this.$refs.infinitescrollSearch.$emit('ydui.infinitescroll.loadedDone');
          }
        }
      })
    },
    findCheckSearchDetail: function (item, state) {
      this.$router.push({
        path: '/processDetail',
        query: {
          processId: item.id,
          checkState: item.state,
          tabState: state,
          checkType: item.checkType
        }
      })
    },
    search:function () {
      this.processSearchList = []
      this.searchInfo.pageNo = 1
      this.$refs.infinitescrollSearch.$emit('ydui.infinitescroll.reInit')
      this.loadprocessSearchList()
    }
  },
  created(){
    this.loadprocessSearchList()
  }
}
</script>

<style scoped>

</style>



