<template>
  <div class="home has_nav_container">
    <div class="home_head">
      <yd-icon class="home_head_left_my" name="my" size="25px" color="#FFF" custom @click.native="mybaseInfo=true"></yd-icon>
      <img class="home_head_logo" src="../assets/img/shtoone.png" />
      <label class="home_head_lab">现场管理系统</label>
      <yd-icon class="home_head_right_message" name="message" size="25px" color="#FFF" custom @click.native="sysMessage"></yd-icon>
    </div>
    <div class="home_lb">

    </div>
    <div class="home_container">
      <yd-flexbox>
        <yd-flexbox-item>
          <!-- 功能按钮 -->
          <yd-grids-group :rows="3">
            <yd-grids-item v-for="(menu, index) in menuList" :key="index" :link="menu.href" v-if="menu.isExist">
              <yd-icon slot="icon" size="35px" :name="menu.name" :color="menu.color" custom></yd-icon>
              <span slot="text">{{menu.text}}</span>
            </yd-grids-item>
          </yd-grids-group>
        </yd-flexbox-item>
      </yd-flexbox>
    </div>
    <yd-popup v-model="mybaseInfo" position="left" width="60%">
      <div class="baseSet_head">
        <label class="baseSet_head_lab">我的</label>
      </div>
      <div class="home_container">
        <div style="text-align: center;margin-top: -24px;">
          <img src="../assets/img/mine/profile.jpg" style="border:1px solid #D0D0D0;border-radius:25px;width: 75px;height: 88px;"/>
        </div>
        <yd-cell-group>
          <yd-cell-item type="label">
            <span slot="left">账名:</span>
            <span slot="right">{{userInfo.username}}</span>
          </yd-cell-item>
          <yd-cell-item type="label">
            <span slot="left">用户名:</span>
            <span slot="right">{{userInfo.realname}}</span>
          </yd-cell-item>
          <yd-cell-item type="label">
            <span slot="left">组织机构:</span>
            <span slot="right">{{userInfo.departName}}</span>
          </yd-cell-item>
          <yd-cell-item type="label">
            <span slot="left">手机号码:</span>
            <span slot="right">{{userInfo.mobilePhone}}</span>
          </yd-cell-item>
          <yd-cell-item type="label">
            <span slot="left">职位:</span>
            <span slot="right">{{userInfo.zhiwei}}</span>
          </yd-cell-item>
          <yd-cell-item type="label" @click.native="version">
            <yd-icon slot="icon" name="download" size="25px" color="#1296db"></yd-icon>
            <span slot="left">检查更新</span>
            <yd-badge type="danger" slot="right">{{currentVersion}}</yd-badge>
          </yd-cell-item>
          <yd-button size="large" type="danger" shape="circle" @click.native="logout">退出</yd-button>
        </yd-cell-group>
      </div>
    </yd-popup>
  </div>

</template>

<script>
  import { checkVersion } from '../api/pgyApi'
  import properties from '../assets/js/properties'
  import { getLoginInfo} from '../assets/js/assist'
  import {logoutCurrentToken, getLoginUserInfo} from '../api/sysUser'

  export default {
    data () {
      return {
        imageAddress: properties.imageAddress,
        mybaseInfo: false,
        currentVersion: '1.0',
        imgUrl: require('../assets/img/main.jpg'),
        menuList: [{
          name: 'processTab',
          color: '#43CF7C',
          href: '/processTab',
          text: '工序验收',
          isExist: this.$ltx.permissionJudge('processTab')
        }, {
          name: 'pollingTab',
          color: '#2A82E4',
          href: '/pollingTab',
          text: '巡视管理',
          isExist: this.$ltx.permissionJudge('pollingTab')
        }, {
          name: 'commandTabs',
          color: '#FF5733',
          href: '/commandTabs',
          text: '指令管理',
          isExist: this.$ltx.permissionJudge('commandTabs')
        }, {
          name: 'footprint',
          color: '#41C6D2',
          href: '/footprint',
          text: '痕迹管理',
          isExist: this.$ltx.permissionJudge('footprint')
        },{
          name: 'noticeList',
          color: '#00923F',
          href: '/noticeList',
          text: '通知管理',
          isExist: this.$ltx.permissionJudge('noticeList')
        },{
          name: 'change',
          color: '#FFCD42',
          href: '/change',
          text: '变更管理',
          isExist: this.$ltx.permissionJudge('change')
        }],
        userInfo:{
          picture: '',
          zhiwei: '',
          username: '',
          realname: '',
          departName: '',
          mobilePhone: ''
        }
      }
    },
    created() {
      if(this.$store.state.updateTip){
        checkVersion()
      }
      if(window.plus){
        this.getCurrentVersion();
      }else{
        document.addEventListener('plusready', this.getCurrentVersion, false);
      }
      this.initLoginUserInfo()
    },
    methods: {
      logout(){
        let userInfo=getLoginInfo();
        logoutCurrentToken(userInfo.account, res=>{
          if(res.ok){
            this.cookies.remove("account");
            this.cookies.remove("password");
            localStorage.clear();
            this.$router.push("/login");
          }
        })
      },
      version(){
        checkVersion(() => {
          this.$dialog.toast({
            mes: '暂无版本更新',
            timeout: 2000
          });
        })
      },
      getCurrentVersion(){
        this.currentVersion = plus.runtime.version
      },
      initLoginUserInfo(){
        getLoginUserInfo(res=>{
          this.userInfo=res.data;
        })
      },
      sysMessage(){
        this.$router.push('/messageList')
      }
    }
  }
</script>

<style scoped>
  .home{
    position: relative;
    height: 100%;
    overflow: hidden;
    background-color: #fff;
  }
  .home_head{
    position: absolute;
    top: 0;
    left: 0;
    z-index: 99;
    width: 100%;
    height: 45px;
    line-height: 45px;
    overflow: hidden;
    text-align: center;
    background: -webkit-linear-gradient(to right, #0B3AC8 , #2B79ED);
    background: -o-linear-gradient(to right, #0B3AC8 , #2B79ED);
    background: -moz-linear-gradient(to right, #0B3AC8 , #2B79ED);
    background: linear-gradient(to right, #0B3AC8 , #2B79ED) ;
  }
  .home_head_logo{
    height: 22px;
    vertical-align: middle;
    margin-top: -4px;
  }
  .home_head_left_my{
    height: 22px;
    float: left;
    vertical-align: middle;
    margin-left: 5px;
  }
  .home_head_right_message{
    height: 22px;
    float: right;
    vertical-align: middle;
    margin-left: 5px;
  }
  .home_head_lab{
    color: #fff;
    font-size: 14px;
  }
  .home_lb{
    position: absolute;
    top: 45px;
    left: 0;
    width: 100%;
    height: 150px;
    z-index: 100;
    background: url("../assets/img/main.jpg") no-repeat center center;
    background-size: 100% 100%;
  }
  .home_lb_item div{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    text-align: center;
    overflow: hidden;
    display: flex;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
  }
  .home_lb_item img{
    max-width: 100%;
    max-height: 100%;
    min-width: 50%;
    min-height: 50%;
    display: inline-block;
    vertical-align: middle;
    margin: auto;
  }
  .home_f1_item img{
    height: 18px;
    vertical-align: middle;
    margin-top: -3px;
  }
  .home_f1_item_w img{
    height: 28px;
    vertical-align: middle;
  }
  .home_floor_head img{
    height: 12px;
    vertical-align: middle;
    margin-top: -3px;
  }
  .home_floor_head label{
    font-size: 12px;
    color: #333;
  }
  .home_floor_head_more img{
    height: 12px;
    vertical-align: middle;
    margin-top: -1px;
  }
  .home_container{
    padding-top: 15px;
    margin-top: 15px;
    padding-bottom: 2px;
    box-sizing: border-box;
    border-top: 5px solid #eee;
    width: 100%;
    height: 100%;
    overflow-y: auto;
  }
  .home_container:before{
    content: "";
    display: inline-block;
    padding-bottom: 33.33%;
    vertical-align: middle;
  }
  .home_ndcz_head_more img, .home_gchgl_head_more img{
    height: 12px;
    vertical-align: middle;
    margin-top: -1px;
  }
  .home_tpxw_img div{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    text-align: center;
    overflow: hidden;
    display: flex;
  }
  .home_tpxw_img img{
    width: 100%;
    height:100%;
    display: inline-block;
    vertical-align: middle;
    margin: auto;
    border-radius: 5px;
  }
  .home_pzgc_item_t img{
    height: 13px;
    vertical-align: middle;
    margin-top: -1px;
  }
  .home_pzgc_item_icon img{
    height: 14px;
  }
  .baseSet_head{
    position: absolute;
    top: 0;
    left: 0;
    z-index: 199;
    width: 100%;
    height: 45px;
    line-height: 45px;
    overflow: hidden;
    text-align: center;
    background: -webkit-linear-gradient(to right, #0B3AC8 , #2B79ED);
    background: -o-linear-gradient(to right, #0B3AC8 , #2B79ED);
    background: -moz-linear-gradient(to right, #0B3AC8 , #2B79ED);
    background: linear-gradient(to right, #0B3AC8 , #2B79ED) ;
  }
  .baseSet_head_lab{
    color: #fff;
    font-size: 14px;
  }
</style>



