/*
  自定义指令
 */
import Vue from 'vue'
import store from '../store'

const ltx={};

ltx.shtoone=Vue.directive('ltx', {
  bind(el, binding, vnode) {
    if (!ltx.permissionJudge(binding.value)) {
      el.parentNode.removeChild(el);
    }
  }
});

ltx.permissionJudge=function(value){
  let list = store.state.buttonList;
  for (let item of list) {
    if (item === value) {
      return true;
    }
  }
  return false;
}

export default ltx



