import {post} from '../axios/ajax'

export const urls = {
  footprintPage: '/mark/chakan'   //痕迹查询
}

/**
 * 痕迹查询
 * @param params
 * @param callback
 */
export const footprintPage = (params, callback) => { post(urls.footprintPage, params, {'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'}, callback) }



