<template>
  <div class="cs_contain has_nav_container">
    <PubNavBarTo title="我的指令" gotoVueName="/commandTabs" :rightFun="this.$ltx.permissionJudge('createCommand')" customeIcon="IssuedCommand" @customeIconFun="createCommand()"></PubNavBarTo>
    <div class="cs_search">
      <yd-flexbox>
        <yd-flexbox-item>
          <yd-cell-group>
            <yd-cell-item>
              <span slot="left">组织机构：</span>
              <yd-input slot="right" v-model="showName.orgName" placeholder="请选择组织机构" :readonly="true" @click.native="selectUserGroup"></yd-input>
            </yd-cell-item>
            <yd-cell-item>
              <span slot="left">分部分项：</span>
              <yd-input slot="right" v-model="showName.projectItemName" placeholder="请选择分部分项" :readonly="true" @click.native="selectProjectItem"></yd-input>
            </yd-cell-item>
          </yd-cell-group>
        </yd-flexbox-item>
        <div style="width: 60px;text-align: center;">
          <div class="huixin_search_btn" @click="search()">搜索</div>
        </div>
      </yd-flexbox>
    </div>
    <div class="cs_main">
      <yd-pullrefresh :callback="loadMyCommandPullRefresh" ref="myCommandPullRefresh">
        <yd-infinitescroll :callback="loadMyCommandInfiniteScroll" ref="infinitescrollMyCommand">
          <yd-list theme="4" slot="list">
            <yd-list-item v-for="(item, key) in myCommandPageList" :key="key" @click.native="findMyCommandDetail(item)">
              <img slot="img" :src="item.filePath"/>
              <span slot="title">{{item.remark}}</span>
              <yd-list-other slot="other">
                <ul>
                  <li class="list-price"><b>发起人:</b>{{item.nowUser}}</li>
                  <li class="list-price"><b>处理人:</b>{{item.launchPerson}}</li>
                  <li class="list-del-price"><b>创建时间:</b>{{item.createTime}}</li>
                  <li class="list-del-price"><b>处理时间:</b>{{item.planTime}}</li>
                  <li class="list-del-price"><b>状态:</b>
                    <template v-if="item.state===0">未处理</template>
                    <template v-else-if="item.state===1">待处理</template>
                    <template v-else-if="item.state===2">完成</template>
                  </li>
                  <li class="list-del-price"><b>位置:</b><yd-icon name="location" size="18px" color="#777"></yd-icon>{{item.photoLocation}}</li>
                </ul>
              </yd-list-other>
            </yd-list-item>
          </yd-list>
          <!-- 数据全部加载完毕显示 -->
          <span slot="doneTip">全部加载完成</span>
        </yd-infinitescroll>
      </yd-pullrefresh>
    </div>

    <!-- 弹框中的组织机构 -->
    <yd-popup v-model="isShowUserGroup" position="center" width="90%">
      <vue-ztree :list.sync='userGroup' :func="ztreeNodeClick" :is-open='true'></vue-ztree>
    </yd-popup>
    <!-- 弹框中的工程分部分项 -->
    <yd-popup v-model="isShowProjectItem" position="center" width="90%">
      <vue-ztree :list.sync='projectItem' :func="ztreeProjectItemNodeClick" :is-open='true'></vue-ztree>
    </yd-popup>
  </div>
</template>

<script>
import PubNavBarTo from '../../../components/pub/NavBarTo'
import {getUserDepartZtree, getProjectItemListByOrgId} from '../../../api/polling'
import {myCommandList} from '../../../api/command'
export default {
  name: "myCommandList",
  data(){
    return{
      isShowProjectItem: false,
      isShowUserGroup: false,
      projectItem: [],   //工程分部分项树
      userGroup: [],  //组织机构树
      showName: {
        orgName: '',
        projectItemName: ''
      },
      myCommandPageList: [],  //数据列表
      queryData: {  //查询条件
        userGroupId: '',
        projectCode: '',  //工程分部分项编码
        pageNo: 1,
        pageSize: 15
      }
    }
  },
  components: {
    PubNavBarTo: PubNavBarTo
  },
  created(){
    this.loadMyCommandInfiniteScroll();
  },
  methods: {
    selectUserGroup: function () {
      this.isShowUserGroup = true
      this.initUserGroupZtree()
    },
    initUserGroupZtree: function () {
      getUserDepartZtree(res => {
        this.userGroup = res.data
      })
    },
    ztreeNodeClick: function (m) {
      m.hover = false
      let node = JSON.parse(JSON.stringify(m))
      if (node.children.length === 0) {
        this.showName.orgName = m.name
        this.queryData.userGroupId = m.id
        this.isShowUserGroup = false
      } else {
        this.$dialog.toast({
          mes: '施工单位下才有分部分项呢',
          timeout: 1500,
          icon: 'error'
        })
      }
    },
    selectProjectItem: function () {
      this.isShowProjectItem = true
      this.initProjectItemZtree(this.queryData.departId)
    },
    ztreeProjectItemNodeClick: function (m) {
      m.hover = false
      this.showName.projectItemName = m.name
      this.queryData.projectCode = m.projectCode
      this.isShowProjectItem = false
    },
    initProjectItemZtree: function (orgId) {
      getProjectItemListByOrgId({orgId: orgId}, res=>{
        this.projectItem = res.data
      })
    },
    createCommand(){  //创建指令
      this.$router.push('/createCommand/0')
    },
    search(){
      this.myCommandPageList=[];
      this.queryData.pageNo=1;
      this.$refs.infinitescrollMyCommand.$emit('ydui.infinitescroll.reInit');  //初始化滚动加载
      this.loadMyCommandInfiniteScroll();
    },
    loadMyCommandPullRefresh(){  //下拉刷新
      myCommandList(this.queryData, res=>{
        if(res.ok) {
          if(res.data && res.data.data.length > 0){
            const _list = res.data.data;
            this.myCommandPageList = [..._list, ...this.myCommandPageList];
            /* 单次请求数据完毕 */
            this.$refs.myCommandPullRefresh.$emit('ydui.pullrefresh.finishLoad');
            this.queryData.pageNo++
          }else{
            /* 所有数据加载完毕 */
            this.$dialog.toast({
              mes:  '已是最新内容'
            });
          }
        }
      })
    },
    loadMyCommandInfiniteScroll(){   //滚动
      myCommandList(this.queryData, res=>{
        if(res.ok) {
          if(res.data && res.data.data.length > 0){
            const _list = res.data.data;
            this.myCommandPageList = [..._list, ...this.myCommandPageList];
            /* 单次请求数据完毕 */
            this.$refs.infinitescrollMyCommand.$emit('ydui.infinitescroll.finishLoad');
            this.queryData.pageNo++
          }else{
            /* 所有数据加载完毕 */
            this.$refs.infinitescrollMyCommand.$emit('ydui.infinitescroll.loadedDone');
          }
        }
      })
    },
    findMyCommandDetail(node){
      this.$router.push({
        path: '/commandDetail',
        query: {
          commandId: node.commandId,
          tabState: node.state,
          issolve: '1'
        }
      })
    }
  }
}
</script>

<style scoped>

</style>



