<template>
  <div class="has_nav_container">
    <PubNavBar title="监理日志详情"></PubNavBar>
    <div class="syjc_main">
      <div class="syjc_map" id="attendMap">
        <baidu-map class="sm_map" :center="{lng: logInfo.pictures[0].lgt, lat: logInfo.pictures[0].lat}" :zoom="zoom">
          <bm-marker :position="{lng: logInfo.pictures[0].lgt, lat: logInfo.pictures[0].lat}" :dragging="true" animation="BMAP_ANIMATION_BOUNCE">
            <bm-label :content="logInfo.pictures[0].photoLocation" :labelStyle="{color:'red', fontSize:'14px', textAlign: 'center'}" :offset="{width: -155, height: 30}"></bm-label>
          </bm-marker>
        </baidu-map>
      </div>
    </div>
    <div class="syjc_form">
      <div class="syjc_form_item">
        <yd-cell-group>
          <yd-cell-item>
            <span slot="left">创建人：</span>
            <span slot="right">{{logInfo.realname}}</span>
          </yd-cell-item>
          <yd-cell-item>
            <span slot="left">创建时间：</span>
            <span slot="right">{{logInfo.createTime}}</span>
          </yd-cell-item>
          <yd-cell-item>
            <span slot="left">日志描述：</span>
            <span slot="right">{{logInfo.describe}}</span>
          </yd-cell-item>
          <yd-cell-item>
            <span slot="left">位置：</span>
            <span slot="right"><yd-icon name="location" size="18px" color="#777"></yd-icon>{{logInfo.pictures[0].photoLocation}}</span>
          </yd-cell-item>
          <yd-cell-item>
            <span slot="left">图片</span>
            <span slot="right">
              <ul v-for="(item, key) in logInfo.pictures" :key="key" class="imgULList">
                <li>
                  <template v-if="item.fileType==='jpg'">
                    <yd-lightbox>
                      <yd-lightbox-img :src="item.filePath" style="width: 72px; height: 72px;"></yd-lightbox-img>
                    </yd-lightbox>
                  </template>
                  <template v-else-if="item.fileType==='mp4'">
                    <video :src="item.filePath" style="width: 60px; height: 60px;" @click="previewVideo(item)"></video>
                  </template>
                </li>
              </ul>
            </span>
          </yd-cell-item>
        </yd-cell-group>
      </div>
    </div>
  </div>
</template>

<script>
import {getSupervisorLogDetail} from '../../api/supervisorLog'
export default {
  name: "supervisorLogDetail",
  data(){
    return{
      logInfo:{
        realname: '',
        describe: '',
        createTime: '',
        photoLocation: '',
        pictures: []
      },
      zoom: 15
    }
  },
  props: {
    logId: {
      type: String
    }
  },
  created(){
    this.loadSupervisorLogDetail()
  },
  methods:{
    loadSupervisorLogDetail:function () {
      getSupervisorLogDetail(this.logId, res=>{
        if(res.ok){
          this.logInfo=res.data
        }
      })
    },
    previewVideo: function (item) {
      this.videoSrc = item.filePath
      this.showVideo = true
    }
  }
}
</script>

<style scoped>
  .syjc_map{
    width: 100%;
    height: 200px;
    text-align: center;
    background-color: #fff;
    border-bottom: 1px solid #eee;
  }
  .sm_map{
    width: 100%;
    height: 100%;
  }
  .imgULList{
    margin: 4px;
    padding: 2px;
    float:left;
    width: 30%;
    text-align: center;
  }
</style>



