<template>
  <div class="has_nav_container">
    <PubNavBar title="创建日志"></PubNavBar>
    <div class="syjc_main">
      <div class="syjc_form">
        <div class="syjc_form_item">
          <yd-cell-group>
            <yd-cell-item>
              <span slot="left">日志描述:</span>
              <yd-textarea slot="right" v-model="log.remark" placeholder="请输入日志描述" maxlength="100"></yd-textarea>
            </yd-cell-item>
          </yd-cell-group>
        </div>
        <div class="syjc_att">
          <Attach :attachList="log.logFileList" :delAttachList="log.delLogFileList" :readonly="false" :sourceType="3"></Attach>
        </div>
        <div class="syjc_bot">
          <yd-button size="large" type="primary" shape="circle" @click.native="resultSuperVisorLog">保存</yd-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Attach from '../../components/pub/Attach'
import { getGeolocation } from '../../assets/plus/geolocation'
import {createSupervisorLog} from '../../api/supervisorLog'
export default {
  data () {
    const self = this
    return {
      log: {
        remark: '',
        logFileList: [],
        delLogFileList: [],
        lng: '',
        lat: '',
        photoLocation: ''
      }
    }
  },
  components: {
    Attach: Attach
  },
  methods: {
    loadLocation(){
      getGeolocation(res => {
        console.log(JSON.stringify(res))
        this.log.photoLocation = res.addresses
        this.log.lng = res.coords.longitude
        this.log.lat = res.coords.latitude
      })
    },
    resultSuperVisorLog: function () {
      if (this.commonFun.isEmptyStr(this.log.remark)) {
        this.$dialog.toast({
          mes: '请填写日志描述',
          timeout: 1500
        })
        return false
      }
      if (this.log.logFileList.length <= 0) {
        this.$dialog.toast({
          mes: '请选择留底图片',
          timeout: 1500
        })
        return false
      }
      let formData = new FormData()
      formData.append('describe', this.log.remark)
      formData.append('lgt', this.log.lng)
      formData.append('lat', this.log.lat)
      formData.append('photoLocation', this.log.photoLocation)
      for (let resourece of this.log.logFileList) {
        if (resourece.isNew) {
          formData.append('files', resourece.file)
        }
      }
      createSupervisorLog(formData, res => {
        if(res.ok) {
          this.$dialog.toast({
            mes: '监理日志提交成功',
            timeout: 1500
          })
          this.$router.push('/supervisorLog')
        }else{
          this.$dialog.toast({
            mes: '提交失败',
            timeout: 2000,
            icon: 'error'
          })
        }
      })
    }
  },
  created(){
    this.loadLocation()
  }
}
</script>

<style scoped>

</style>



