import axios from 'axios'
import properties from '../assets/js/properties'
import router from '../router'
import {getLoginToken} from '../assets/js/assist'

//请求地址前缀
axios.defaults.baseURL = (process.env.NODE_ENV === 'development'?'/api':properties.reqAddress)

// 请求前拦截器
axios.interceptors.request.use(function(config) {
  // 让每个请求头部都携带token信息
  let getTokenVal= getLoginToken()
  if(getTokenVal==='' || getTokenVal===null || getTokenVal===undefined){
    if(config.url==='/tokens'){
      return config
    }else {
      router.replace({
        path: '/',
        query: {
          redirect: router.currentRoute.fullPath
        }
      })
    }
  }else{
    config.headers['X-AUTH-TOKEN'] =getTokenVal
    return config
  }
}, function(error) {
	return Promise.reject(error)
});

// Add a response interceptor
axios.interceptors.response.use(function(response) {
	if(response.data) {
		return response
	} else {
		console.log('response error');
		return Promise.reject(new Error('response error'))
	}
}, function(error) {
	return Promise.reject(error)
});

export default axios



