import { setLoginUser, removeLoginUser, setToken, removeToken } from '../assets/js/assist'
import {getButtonPermissionByToken} from '../api/sysUser'

export default {
  switch_dialog(context){//这里的context和我们使用的$store拥有相同的对象和方法
    context.commit('switch_dialog');
    //你还可以在这里触发其他的mutations方法
  },
  setUserInfo: (context, userInfo) => {
    setLoginUser(userInfo)
    context.commit('setUserInfo', userInfo)
  },
  setToken: (context, token) => {
    setToken(token)
    context.commit('setToken', token)
  },
  clearUserInfo: (context) => {
    removeLoginUser()
    context.commit('clearUserInfo')
  },
  clearToken: (context) => {
    removeToken()
    context.commit('clearToken')
  },
  getPermissionButton({commit}){
    return new Promise((resolve, reject) => {
      getButtonPermissionByToken(response => {
        const buttonData = response.data;
        if (buttonData && buttonData.length > 0) {
          commit('SET_BUTTONLIST', buttonData);
        } else {
          reject('buttonList: button列表不是一个空的数组 !')
        }
        resolve(response)
      });
    });
  }
}



