import Vue from 'vue';
import Vuex from 'vuex';
import mutations from './mutations';
import actions from './actions';
import getters from './getters';

Vue.use(Vuex);

export default new Vuex.Store({
	state: {
		isShow: false,//显示加载中
		show: false,//测试
		userInfo: {},//登录信息
    token: '',   //用户token
		direction: 'slide-left',
		updateTip: true, //提示版本更新
    buttonList:[]
	},
  modules: {
  },
  mutations: mutations,
  actions: actions,
  getters: getters
});



