import { post, upload} from '../axios/ajax'

export const urls = {
  supervisorList: '/processInfoLog/everyDayLogPageList',    //日志列表
  createSupervisorLog: '/processInfoLog/everyDayLog',       //创建日志
  showSupervisorLogDetail: '/processInfoLog/findLog'        //查看日志详情
}

/**
 * 监理日志查询
 * @param params
 * @param callback
 */
export const getSupervisorLogList = (params, callback) => { post(urls.supervisorList, params, {'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'}, callback) }

/**
 * 创建监理日志
 * @param params
 * @param callback
 */
export const createSupervisorLog = (params, callback) => { upload(urls.createSupervisorLog, params, callback) }

/**
 * 监理日志详情
 * @param params
 * @param callback
 */
export const getSupervisorLogDetail = (params, callback) => { post(urls.showSupervisorLogDetail + '/' + params, {}, {'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'}, callback) }



