<template>
  <div class="has_nav_container">
    <PubNavBar title="验收"></PubNavBar>
    <div class="syjc_main">
      <div class="syjc_form">
        <div class="syjc_form_item">
          <yd-cell-group>
            <yd-cell-item>
              <span slot="left">分部分项：</span>
              <span slot="right">{{processInfoDetail.projectItem}}</span>
            </yd-cell-item>
            <yd-cell-item>
              <span slot="left">桩号：</span>
              <span slot="right">{{processInfoDetail.zhuanghao}}</span>
            </yd-cell-item>
            <yd-cell-item>
              <span slot="left">工序名称：</span>
              <span slot="right">{{processInfoDetail.processName}}</span>
            </yd-cell-item>
            <yd-cell-item>
              <span slot="left">状态：</span>
              <span slot="right">
                <template v-if="checkState===1">待自检</template>
                <template v-else-if="checkState===2">待验收</template>
                <template v-else-if="checkState===3">验收完成</template>
              </span>
            </yd-cell-item>
            <fieldset style="border: 1px dashed #E6E6E6; margin: 10px; border-radius: 10px;">
              <legend>自检</legend>
              <yd-cell-item>
                <span slot="left">计划时间：</span>
                <span slot="right">{{processInfoDetail.planSelfCheckTime}}</span>
              </yd-cell-item>
              <yd-cell-item>
                <span slot="left">计划人：</span>
                <span slot="right">{{processInfoDetail.planSelfCheckPerson}}</span>
              </yd-cell-item>
              <yd-cell-item>
                <span slot="left">实际时间：</span>
                <span slot="right">{{processInfoDetail.realitySelfCheckTime}}</span>
              </yd-cell-item>
              <yd-cell-item>
                <span slot="left">实际人：</span>
                <span slot="right">{{processInfoDetail.realitySelfCheckPerson}}</span>
              </yd-cell-item>
              <yd-cell-item>
                <span slot="left">描述：</span>
                <span slot="right">{{processInfoDetail.selfCheckDescribe}}</span>
              </yd-cell-item>
              <yd-cell-item>
                <span slot="left">影像资料</span>
                <span slot="right">
                  <ul v-for="(item, key) in processInfoDetail.selfFilePath" :key="key" class="imgULList">
                    <li>
                      <template v-if="item.fileType==='jpg'">
                        <yd-lightbox>
                          <yd-lightbox-img :src="item.filePath" style="width: 60px; height: 60px;"></yd-lightbox-img>
                        </yd-lightbox>
                      </template>
                      <template v-else-if="item.fileType==='mp4'">
                        <video :src="item.filePath" style="width: 60px; height: 60px;" @click="previewVideo(item)"></video>
                      </template>
                    </li>
                  </ul>
                </span>
              </yd-cell-item>
            </fieldset>
            <fieldset style="border: 1px dashed #E6E6E6; margin: 10px; border-radius: 10px;">
              <legend>验收</legend>
              <yd-cell-item>
                <span slot="left">计划时间：</span>
                <span slot="right">{{processInfoDetail.planCheckTime}}</span>
              </yd-cell-item>
              <yd-cell-item>
                <span slot="left">计划人：</span>
                <span slot="right">{{processInfoDetail.planCheckPerson}}</span>
              </yd-cell-item>
              <yd-cell-item>
                <span slot="left">验收描述：</span>
                <yd-textarea slot="right" required v-model="checkProcess.remark" placeholder="请输入验收描述" maxlength="100"></yd-textarea>
              </yd-cell-item>
            </fieldset>
          </yd-cell-group>
        </div>
        <div class="syjc_att">
          <yd-cell-group>
            <yd-cell-item>
              <span slot="left">照片描述:</span>
              <yd-textarea slot="right" v-model="checkProcess.photoDescribe" placeholder="请输入照片描述" maxlength="100"></yd-textarea>
            </yd-cell-item>
          </yd-cell-group>
          <Attach :attachList="checkProcess.realProgressFile" :delAttachList="checkProcess.delProgressList" :readonly="false" :sourceType="2"></Attach>
        </div>
        <div class="syjc_bot">
          <yd-button type="primary" style="float: left;" shape="circle" @click.native="supervisionCheckFun('1')">通过</yd-button>
          <yd-button type="danger"  style="float: right;" shape="circle" @click.native="supervisionCheckFun('0')">不通过</yd-button>
        </div>
      </div>
    </div>
    <!--音频播放-->
    <yd-popup v-model="showVideo" position="center" width="90%">
      <div class="huixin_video">
        <div><video id="previewVideo" :src="videoSrc" autoplay controls></video></div>
      </div>
      <div class="huixin_close">
        <yd-button @click.native="showVideo = false">关闭</yd-button>
      </div>
    </yd-popup>
  </div>
</template>

<script>
import Attach from '../../../../components/pub/Attach'
import PubNavBarTo from '../../../../components/pub/NavBarTo'
import {getProcessDetail, supervisionCheck} from '../../../../api/processCheck'
import {formatDate} from '../../../../assets/js/assist'
import { getGeolocation } from '../../../../assets/plus/geolocation'
export default {
  name: "checkTask",
  data(){
    return {
      processId: '',
      checkState: '',
      showVideo: false,
      videoSrc: '',
      defaultPlanCheckTime: '',
      processInfoDetail:{},
      realityCheckPerson: '',
      isShowRealityCheckPerson: false,
      realityCheckPersonList: [],
      checkProcess: {
        remark: '',
        photoDescribe: '',
        realProgressFile: [],
        delProgressList: [],
        lng: '',
        lat: '',
        photoLocation: '',
      },
      pageNo: 1,
      pageSize: 15,
      orgId: ''
    }
  },
  components: {
    PubNavBarTo: PubNavBarTo,
    Attach: Attach
  },
  methods:{
    loadLocation(){
      getGeolocation(res => {
        this.checkProcess.photoLocation = res.addresses
        this.checkProcess.lng = res.coords.longitude
        this.checkProcess.lat = res.coords.latitude
      })
    },
    initProcessDetail: function () {
      getProcessDetail({id: this.processId}, res=>{
        console.log(res.data)
        this.processInfoDetail=res.data
      })
    },
    supervisionCheckFun: function (state) {  //通过不通过
      if (this.commonFun.isEmptyStr(this.checkProcess.remark)) {
        this.$dialog.toast({
          mes: '请填写验收描述',
          timeout: 1500
        })
        return false
      }
      if (this.checkProcess.realProgressFile.length <= 0) {
        this.$dialog.toast({
          mes: '请选择留底图片',
          timeout: 1500
        })
        return false
      }
      let formData = new FormData()
      formData.append('processMainId', this.processId)
      formData.append('processId', this.processInfoDetail.processLogId)
      formData.append('remark', this.checkProcess.remark)
      formData.append('lgt', this.checkProcess.lng)
      formData.append('lat', this.checkProcess.lat)
      formData.append('photoLocation', this.checkProcess.photoLocation)
      formData.append('photoDescribe', this.checkProcess.photoDescribe)
      formData.append('type', 'task')
      if(state==='1'){  //通过
        formData.append('mark', 3)
      }else{
        formData.append('mark', 2)
      }
      formData.append('adopt', state)
      for (let resourece of this.checkProcess.realProgressFile) {
        if (resourece.isNew) {
          formData.append('files', resourece.file)
        }
      }
      supervisionCheck(formData, res=>{
        if(res.ok){
          this.$dialog.toast({
            mes: '验收成功',
            timeout: 1500
          })
          this.$router.push('/checkProcess')
        }else{
          this.$dialog.toast({
            mes: '验收失败',
            timeout: 2000,
            icon: 'error'
          })
        }
      })
    }
  },
  created(){
    this.processId=this.$route.query.processId
    this.checkState=this.$route.query.checkState
    this.initProcessDetail()
    this.loadLocation()
    this.defaultPlanCheckTime = formatDate((new Date()))
  }
}
</script>

<style scoped>
.imgULList{
  float: left;
  margin: 10px;
  padding: 2px;
  width: 30%;
  text-align: center;
}
</style>



