import Vue from 'vue'
import Router from 'vue-router'
import store from '../store'

Vue.use(Router)

import Tabs from '../pages/Tabs'  //tab页
import Login from '../pages/Login'  //登录页
/** 监理日志 ***/
import supervisorLog from '../pages/supervisorLog/supervisorLog'  //监理日志数据列表
import createLog from '../pages/supervisorLog/createLog'   //创建监理日志
import supervisorLogDetail from '../pages/supervisorLog/supervisorLogDetail'  //监理日志详情
/** 巡查 ***/
import pollingTab from '../pages/polling/pollingTab'  //巡视导航
import pollingList from '../pages/polling/polling/pollingList'     //巡检记录列表
import createPolling from '../pages/polling/polling/polling'    //创建巡检
import pollingDetail from '../pages/polling/polling/pollingDetail'  //巡检记录详情
/** 旁站巡查 ***/
import sideStationList from '../pages/polling/sideStation/sideStationList'   //旁站巡视
import createSideStation from '../pages/polling/sideStation/sideStation'  //创建旁站巡视
import sideStationDetail from '../pages/polling/sideStation/sideStationDetail'   //旁站巡视记录
/** 工序验收 ***/
import processTab from '../pages/process/processTabs'    //工序验收导航
//指定验收--
import checkPlan from '../pages/process/appointCheck/checkPlan'   //罗列所有生成的计划
import appointCheck from '../pages/process/appointCheck/appointCheck'   //指定验收中指定工序
import selectCheckPerson from '../pages/process/appointCheck/selectCheckPerson'   //指定验收中生成验收计划
//验收任务
import processCheckTabs from '../pages/process/processCheck/processCheckTabs'  //工序验收
import receiveProcessTask from '../pages/process/processCheck/receiveProcessTask'    //工序验收任务---收到
import sendProcessTask from '../pages/process/processCheck/sendProcessTask'    //工序验收任务---发出
import selfCheckTask from '../pages/process/processCheck/operate/selfCheckTask'   //自检任务
import checkTask from '../pages/process/processCheck/operate/checkTask'   //验收任务
//验收查询
import processSearch from '../pages/process/processSearch/processSearch'   //验收查询
import processDetail from '../pages/process/processDetail'   //验收详情
/** 指令 ***/
import commandTabs from '../pages/command/commandTabs'    //指令管理导航
//我的指令
import myCommand from '../pages/command/myCommand/myCommandList'  //我的指令列表
import createCommand from '../pages/command/myCommand/createCommand'    //创建指令
//指令任务
import commandTaskTabs from '../pages/command/commandTask/commandTaskTabs'    //指令任务
import receiveCommandTask from '../pages/command/commandTask/receiveCommandTask'   //收到的指令任务
import sendCommandTask from '../pages/command/commandTask/sendCommandTask'  //发出的指令任务
import transpondCommand from '../pages/command/commandTask/operate/transpondCommand'  //转发
import finishCommand from '../pages/command/commandTask/operate/finishCommand'   //完成
//指令查询
import commandSearch from '../pages/command/commandSearch/commandSearch'    //指令查询
import commandDetail from '../pages/command/commandDetail'   //指令详情

/** 痕迹 **/
import footprint from '../pages/footprint/footprint'   //痕迹
/** 系统消息 **/
import messageList from '../pages/message/messageList'  //消息列表
/** 通知管理 **/
import noticeList from '../pages/notice/noticeList'   //通知管理
import createNotice from '../pages/notice/createNotice'  //创建通知
import noticeDetail from '../pages/notice/noticeDetail'  //通知详情



const router = new Router({
  routes: [{
    path: '/', name: 'Login', component: Login, meta:{ title:"登录"}
  }, {
	  path: '/tab', name: 'Tabs', component: Tabs, meta: {title: '主页'}
  }, {
    path: '/supervisorLog', name: 'supervisorLog', component: supervisorLog, meta: {title: '监理日志列表'}
  }, {
    path: '/createLog', name: 'createLog', component: createLog, meta: {title: '创建日志'}
  }, {
    path: '/supervisorLogDetail/:logId', name: 'supervisorLogDetail', component: supervisorLogDetail, props: true, meta: {title: '日志详情'}
  }, {
    path: '/pollingTab', name: 'pollingTab', component: pollingTab, meta: {title: '巡视导航'}
  }, {
    path: '/pollingList', name: 'pollingList', component: pollingList, meta: {title: '巡视列表'}
  }, {
    path: '/sideStationList', name: 'sideStationList', component: sideStationList, meta: {title: '旁站列表'}
  }, {
    path: '/createSideStation', name: 'createSideStation', component: createSideStation, meta: {title: '创建旁站巡视'}
  }, {
    path: '/sideStationDetail/:pollingId', name: 'sideStationDetail', component: sideStationDetail, props: true, meta: {title: '创建旁站巡视详情'}
  }, {
    path: '/createPolling', name: 'createPolling', component: createPolling, meta: {title: '创建巡检'}
  }, {
    path: '/pollingDetail/:pollingId', name: 'pollingDetail', component: pollingDetail, props: true, meta: {title: '巡检详情'}
  }, {
    path: '/processTab', name: 'processTab', component: processTab, meta: {title: '工序导航'}
  }, {
    path: '/processCheckTabs', name: 'processCheckTabs', component: processCheckTabs, meta: {title: '验收任务tabItem'},
    children: [
      { path: '', component: receiveProcessTask, name: '', meta: {title: '收到验收任务'} },
      {path: '/receiveProcessTask', name: 'receiveProcessTask', component: receiveProcessTask, meta: {title: '收到验收任务'} },
      {path: '/sendProcessTask', name: 'sendProcessTask', component: sendProcessTask, meta: {title: '发出验收任务'} }
    ]
  }, {
    path: '/checkPlan', name: 'checkPlan', component: checkPlan, meta: {title: '验收计划列表'}
  }, {
    path: '/appointCheck', name: 'appointCheck', component: appointCheck, meta: {title: '指定验收之指定工序'}
  }, {
    path: '/selectCheckPerson', name: 'selectCheckPerson', component: selectCheckPerson, meta: {title: '指定验收之指定人验收或自检'}
  }, {
    path: '/processDetail', name: 'processDetail', component: processDetail, meta: {title: '工序验收详情'}
  },{
    path: '/processSearch', name: 'processSearch', component: processSearch, meta: {title: '验收查询'}
  }, {
    path: '/selfCheckTask', name: 'selfCheckTask', component: selfCheckTask, meta: {title: '监理验收'}
  }, {
    path: '/checkTask', name: 'checkTask', component: checkTask, meta: {title: '施工单位验收'}
  }, {
    path: '/commandTabs', name: 'commandTabs', component: commandTabs, meta: {title: '指令导航'}
  }, {
    path: '/myCommand', name: 'myCommand', component: myCommand, meta: {title: '我发出的指令列表'}
  }, {
    path: '/createCommand/:pollingId', name: 'createCommand', component: createCommand, props: true, meta: {title: '创建指令'}
  }, {
    path: '/commandTaskTabs', name: 'commandTaskTabs', component: commandTaskTabs, meta: {title: '指令任务列表'},
    children: [
      { path: '', component: receiveCommandTask, name: '', meta: {title: '收到指令任务'} },
      {path: '/receiveCommandTask', name: 'receiveCommandTask', component: receiveCommandTask, meta: {title: '收到指令任务'} },
      {path: '/sendCommandTask', name: 'sendCommandTask', component: sendCommandTask, meta: {title: '发出指令任务'} }
    ]
  }, {
    path: '/commandDetail', name: 'commandDetail', component: commandDetail, meta: {title: '指令详情'}
  }, {
    path: '/transpondCommand', name: 'transpondCommand', component: transpondCommand, meta: {title: '转发指令'}
  }, {
    path: '/finishCommand', name: 'finishCommand', component: finishCommand, meta: {title: '完成指令'}
  }, {
    path: '/commandSearch', component: commandSearch, name: 'commandSearch', meta: {title: '指令查询'}
  }, {
    path: '/footprint', name: 'footprint', component: footprint, meta: {title: '痕迹'}
  }, {
    path: '/messageList', name: 'messageList', component: messageList, meta: {title: '系统消息列表'}
  }, {
    path: '/noticeList', name: 'noticeList', component: noticeList, meta: {title: '通知消息列表'}
  }, {
    path: '/createNotice', name: 'createNotice', component: createNotice, meta: {title: '创建通知消息'}
  }, {
    path: '/noticeDetail', name: 'noticeDetail', component: noticeDetail, meta: {title: '通知详情'}
  }]
});


const history = window.sessionStorage;
history.clear()
let historyCount = history.getItem('pageCount') * 1 || 0;
history.setItem('/', 0);

// 登录中间验证，页面需要登录而没有登录的情况直接跳转登录
router.beforeEach((to, from, next) => {
	const toIndex = history.getItem(to.path);
	const fromIndex = history.getItem(from.path);
	if (toIndex) {
	    if (!fromIndex || parseInt(toIndex, 10) > parseInt(fromIndex, 10) || (toIndex === '0' && fromIndex === '0')) {
	      store.commit('updateDirection', 'slide-left')
	    } else {
	      store.commit('updateDirection', 'slide-right')
	    }
	} else {
	    ++historyCount;
	    history.setItem('pageCount', historyCount);
	    to.path !== '/' && history.setItem(to.path, historyCount);
	    store.commit('updateDirection', 'slide-left')
	}

	if(to.meta.title) {
    document.title = to.meta.title;
  }

	if (to.matched.length ===0) { //如果未匹配到路由
    	next('/');
  }else{
	  //一般我们没有requiresAuth
    if (to.matched.some(record => record.meta.requiresAuth)) {
      if (store.state.token) {
        next();
      } else {
        next({
          path: '/',
          query: { redirect: to.fullPath }
        });
      }
    } else {
      next();
    }
  }
});

export default router;



