<template>
  <div class="cs_contain has_nav_container">
    <PubNavBarTo title="指令管理" gotoVueName="/tab" ></PubNavBarTo>
    <div class="tab_main">
      <!-- 功能按钮 -->
      <yd-grids-group :rows="3">
        <yd-grids-item v-for="(menu, index) in menuList" :key="index" :link="menu.href" >
          <yd-icon slot="icon" size="35px" :name="menu.name" :color="menu.color" custom></yd-icon>
          <span slot="text">{{menu.text}}</span>
        </yd-grids-item>
      </yd-grids-group>
    </div>
  </div>
</template>

<script>
  import PubNavBarTo from '../../components/pub/NavBarTo'
  export default {
    name: "pollingTabs",
    components: {
      PubNavBarTo: PubNavBarTo
    },
    data(){
      return {
        menuList: [{
          name: 'myCommand',
          color: '#43CF7C',
          href: '/myCommand',
          text: '我的指令'
        }, {
          name: 'commandTaskTabs',
          color: '#CC0001',
          href: '/commandTaskTabs',
          text: '指令任务'
        },{
          name: 'commandSearch',
          color: '#FBD670',
          href: '/commandSearch',
          text: '指令查询'
        }]
      }
    }
  }
</script>

<style scoped>
  .tab_main{
    margin-bottom: 60px;
    margin-top: 48%;
  }
</style>



