var back = null;
function geolocationPlusReady(url){
	plus.geolocation.getCurrentPosition( geoInfo, function (e) {
    switch(e.code) {
      case e.PERMISSION_DENIED:
        plus.nativeUI.toast('访问权限被拒绝');
        break;
      case e.POSITION_UNAVAILABLE:
        plus.nativeUI.toast('位置信息不可用');
        break;
      case e.TIMEOUT:
        plus.nativeUI.toast('获取位置信息超时');
        break;
      case e.UNKNOWN_ERROR:
        plus.nativeUI.toast('未知错误');
        break;
    }
		back({success: false, msg: "获取定位位置信息失败："+e.message});
	},{geocode:true, enableHighAcuracy: true, maximumAge: 3000});
}

function geoInfo(position) {
	var str = "";
	str += "地址："+position.addresses+"\n";//获取地址信息
	str += "坐标类型："+position.coordsType+"\n";
	var timeflag = position.timestamp;//获取到地理位置信息的时间戳；一个毫秒数；
	str += "时间戳："+timeflag+"\n";
	var codns = position.coords;//获取地理坐标信息；
	var lat = codns.latitude;//获取到当前位置的纬度；
	str += "纬度："+lat+"\n";
	if(codns.longitude===undefined || codns.longitude==="" || codns.longitude===null){
	  plus.nativeUI.toast('请开启定位权限');
  }else{
    var longt = codns.longitude;//获取到当前位置的经度
    str += "经度："+longt+"\n";
    var alt = codns.altitude;//获取到当前位置的海拔信息；
    str += "海拔："+alt+"\n";
    var accu = codns.accuracy;//地理坐标信息精确度信息；
    str += "精确度："+accu+"\n";
    var altAcc = codns.altitudeAccuracy;//获取海拔信息的精确度；
    str += "海拔精确度："+altAcc+"\n";
    var head = codns.heading;//获取设备的移动方向；
    if(isNaN(head) || head===null){
      head="";
    }
    str += "移动方向："+head+"\n";
    var sped = codns.speed;//获取设备的移动速度；
    str += "移动速度："+sped;
    console.log(str);
  }
  back(position)
}

export const getGeolocation = function(callback){
	back = callback;
	if(window.plus){
		geolocationPlusReady();
	}else{
		document.addEventListener('plusready', geolocationPlusReady, false);
	}
}



