<template>
  <div class="cs_contain has_nav_container">
    <PubNavBar title="指定验收计划" :rightFun="this.$ltx.permissionJudge('appointCheck')" customeIcon="createCheckPlan" @customeIconFun="appointCheckFun()"></PubNavBar>
    <div class="cs_search">
      <yd-flexbox>
        <yd-flexbox-item>
          <yd-cell-group>
            <yd-cell-item>
              <span slot="left">组织机构：</span>
              <yd-input slot="right" v-model="showName.orgName" placeholder="请选择组织机构" :readonly="true" @click.native="selectUserGroup"></yd-input>
            </yd-cell-item>
            <yd-cell-item>
              <span slot="left">分部分项：</span>
              <yd-input slot="right" v-model="showName.projectItemName" placeholder="请选择分部分项" :readonly="true" @click.native="selectProjectItem"></yd-input>
            </yd-cell-item>
          </yd-cell-group>
        </yd-flexbox-item>
        <div style="width: 60px;text-align: center;">
          <div class="huixin_search_btn" @click="search()">搜索</div>
        </div>
      </yd-flexbox>
    </div>
    <div class="cs_main">
      <yd-pullrefresh :callback="loadCheckPlanPullRefresh" ref="checkPlanPullRefresh">
        <yd-infinitescroll :callback="loadAllCheckPlanList" ref="infinitescrollCheckPlan">
          <div class="huixin_item" slot="list"  v-for="(item, index) in checkPlanList" :key="index" @click="findProcessDetail(item)">
            <ul>
              <li><span style="font-weight: bold;">工序名:</span>{{item.processName}}</li>
              <li><span style="font-weight: bold;">计划自检人:</span>{{item.planSelfCheckTime}}</li>
              <li><span style="font-weight: bold;">计划验收人:</span>{{item.planCheckTime}}</li>
              <li><span style="font-weight: bold;">创建时间:</span>{{item.createtime}}</li>
              <li><span style="font-weight: bold;">状态:</span>
                <template v-if="item.state2===0">已指定工序</template>
                <template v-else-if="item.state2===1">已指定验收计划</template>
                <template v-else-if="item.state2===2">已自检</template>
                <template v-else-if="item.state2===3">已验收</template>
              </li>
            </ul>
          </div>
          <!-- 数据全部加载完毕显示 -->
          <span slot="doneTip">全部加载完成</span>
        </yd-infinitescroll>
      </yd-pullrefresh>
    </div>

    <!-- 弹框中的组织机构 -->
    <yd-popup v-model="isShowUserGroup" position="center" width="90%">
      <vue-ztree :list.sync='userGroup' :func="ztreeNodeClick" :is-open='true'></vue-ztree>
    </yd-popup>
    <!-- 弹框中的工程分部分项 -->
    <yd-popup v-model="isShowProjectItem" position="center" width="90%">
      <vue-ztree :list.sync='projectItem' :func="ztreeProjectItemNodeClick" :is-open='true'></vue-ztree>
    </yd-popup>
  </div>
</template>

<script>
import {getUserDepartZtree, getProjectItemListByOrgId} from '../../../api/polling'
import {getAllCheckPlanPage} from '../../../api/processCheck'
export default {
  name: "checkPlan",
  data () {
    return {
      isShowProjectItem: false,
      isShowUserGroup: false,
      projectItem: [],
      userGroup: [],
      showName: {
        orgName: '',
        projectItemName: ''
      },
      queryData: {  //查询条件
        departId:'',
        projectItemCode: '',
        pageNo: 1,
        pageSize: 15
      },
      checkPlanList: []  //罗列所有的验收计划
    }
  },
  created(){
    this.loadAllCheckPlanList();
  },
  methods: {
    selectUserGroup: function () {
      this.isShowUserGroup = true
      this.initUserGroupZtree()
    },
    initUserGroupZtree: function () {
      getUserDepartZtree(res => {
        this.userGroup = res.data
      })
    },
    ztreeNodeClick: function (m) {
      m.hover = false
      let node = JSON.parse(JSON.stringify(m))
      if (node.children.length === 0) {
        this.showName.orgName = m.name
        this.queryData.departId = m.id
        this.isShowUserGroup = false
      } else {
        this.$dialog.toast({
          mes: '施工单位下才有分部分项呢',
          timeout: 1500,
          icon: 'error'
        })
      }
    },
    selectProjectItem: function () {
      this.isShowProjectItem = true
      this.initProjectItemZtree(this.this.queryData.departId)
    },
    ztreeProjectItemNodeClick: function (m) {
      m.hover = false
      this.showName.projectItemName = m.name
      this.queryData.projectItemCode = m.projectCode
      this.isShowProjectItem = false
    },
    initProjectItemZtree: function (orgId) {
      getProjectItemListByOrgId({orgId: orgId}, res=>{
        this.projectItem = res.data
      })
    },
    appointCheckFun(){   //制定验收计划
      this.$router.push('/appointCheck')
    },
    loadCheckPlanPullRefresh(){   //下拉刷新
      getAllCheckPlanPage(this.queryData, res=>{
        if(res.ok) {
          if(res.data && res.data.data.length > 0){
            const _list = res.data.data;
            this.checkPlanList = [..._list, ...this.checkPlanList];
            /* 单次请求数据完毕 */
            this.$refs.checkPlanPullRefresh.$emit('ydui.pullrefresh.finishLoad');
            this.queryData.pageNo++
          }else{
            /* 所有数据加载完毕 */
            this.$dialog.toast({
              mes:  '已是最新内容'
            });
          }
        }
      })
    },
    loadAllCheckPlanList(){  //滑动刷新
      getAllCheckPlanPage(this.queryData, res=>{
        if(res.ok) {
          if(res.data && res.data.data.length > 0){
            const _list = res.data.data;
            this.checkPlanList = [..._list, ...this.checkPlanList];
            /* 单次请求数据完毕 */
            this.$refs.infinitescrollCheckPlan.$emit('ydui.infinitescroll.finishLoad');
            this.queryData.pageNo++
          }else{
            /* 所有数据加载完毕 */
            this.$refs.infinitescrollCheckPlan.$emit('ydui.infinitescroll.loadedDone');
          }
        }
      })
    },
    search(){
      this.checkPlanList=[];
      this.queryData.pageNo=1;
      this.$refs.infinitescrollCheckPlan.$emit('ydui.infinitescroll.reInit');  //初始化滚动加载
      this.loadAllCheckPlanList();
    },
    findProcessDetail(item){  //查看每一个项
      this.$router.push({
        path: '/processDetail',
        query: {
          processId: item.id,
          checkState: item.state2
        }
      })
    }
  }
}
</script>

<style scoped>
</style>



